<template>
   <div class="warning-add">
       <p class="warning-add-title">基础选项</p>
       <el-form ref="form" :model="form" :rules="rules" label-width="150px">
            <el-form-item label="策略名称：" prop="title">
                <el-input v-model="form.title" clearable></el-input>
            </el-form-item>
           <el-form-item label="IP地址：" prop="title">
               <el-col :span="10">
                    <el-select v-model="value" placeholder="请选择">
                        <el-option
                        label="IP地址"
                        value="IP地址">
                        </el-option>
                    </el-select>
               </el-col>
               <el-col :span="2"><p style="dispaly:inline-block;width:10px;"></p></el-col>
               <el-col :span="14">
                <el-input v-model="form.phone" clearable style="width:100%"></el-input>
               </el-col>
            </el-form-item>
            <el-form-item label="网关地址：" prop="title">
                <el-input v-model="form.email" clearable></el-input>
            </el-form-item>
            <el-form-item label="子网掩码：" prop="title">
                <el-input v-model="form.content" clearable></el-input>
            </el-form-item>
            <el-form-item label="端口类型：" prop="title">
                 <el-select v-model="value" placeholder="请选择" style="width:100%">
                    <el-option
                    label="端口类型"
                    value="端口类型">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="IP地址规则：" prop="title">
                <el-select v-model="value" placeholder="请选择" style="width:100%">
                    <el-option
                    label="端口类型"
                    value="端口类型">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="网关规则：" prop="title">
                 <el-select v-model="value" placeholder="请选择" style="width:100%">
                    <el-option
                    label="网关规则"
                    value="网关规则">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="子网掩码规则：" prop="title">
                 <el-select v-model="value" placeholder="请选择" style="width:100%">
                    <el-option
                    label="子网掩码规则"
                    value="子网掩码规则">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('form')">增加</el-button>
                <el-button>取消</el-button>
            </el-form-item>
        </el-form>
   </div>
</template>
<script>
    export default{
        data(){
           
            return{
                form:{
                    title:'',
                    type:'1',
                    phone:'',
                    email:'',
                    content:'',
                    status:'',
                    delivery:''
                },
                 rules: {
                    title: [
                        { required: true, message: '请输入名称', trigger: 'blur' },
                    ],
                }
            }
        },
        components:{
        },
        methods: {
          
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
            },
        }
    }
</script>
<style scoped lang="scss">
   .warning-add{
       padding: 0 80px;
       .warning-add-title{
           font-size: 28px;
           font-weight: 600;
           height: 60px;
           line-height: 60px;
       }
   }
</style>


