// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

// import './assets/style.css'
import './assets/ionicons/css/ionicons.min.css'

import ElementUI from 'element-ui' //新添加
import 'element-ui/lib/theme-chalk/index.css'

import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import echarts from 'echarts'

import apis from '@/utils/api'
import commTool from '@/utils/commTool'
import Router from 'vue-router'
import JSEncrypt from 'jsencrypt';
import JsonViewer  from 'vue-json-viewer'
import LaravelEcho from '@/utils/laravel-echo'
import 'element-ui/lib/theme-chalk/display.css';

window.capis = apis;
window.ctool = commTool;

Vue.use(ElementUI)   //新添加
Vue.use(Vuex) //
Vue.use(JsonViewer) //
// Vue.use(LaravelEcho)
Vue.config.productionTip = false

Vue.prototype.$echarts = echarts


const store = new Vuex.Store({
    state:{
      count: 101
    },
    mutations:{
      countIncrease(state){
        state.count++
      }
    }
});
 
Vue.prototype.$getRsaCode = function(str){ // 注册方法
  let pubKey = `-----BEGIN PUBLIC KEY-----
MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCwBu90de8YlDxus4jjZVFnThT7
GDyv46dRumy46RZQiPrnTIA6vM8n/8TURCR4bUqiXlFPEIYySd3Ij6BYMiiv9crp
0zQ4+Zq4v4QkQjACKHWx8mH1AmT5m8ve6tOtktvxsANykkmIM8KLP2tX5CnsOWdb
5QSwPwoS7kRE75r9ZwIDAQAB
-----END PUBLIC KEY-----`;// ES6 模板字符串 引用 rsa 公钥
  let encryptStr = new JSEncrypt();
  encryptStr.setPublicKey(pubKey); // 设置 加密公钥
  let  data = encryptStr.encrypt(str.toString());  // 进行加密
  return data;
}

Vue.prototype.$LaravelEcho = LaravelEcho;



/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})

router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})



