<!--
 * @Descripttion: 告警与报错
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 10:13:41
-->
<template>
    <div class="warning">
        <div class="message-top" ref="message-top">
           <el-form :inline="true" :model="form" class="demo-form-inline" @keydown.enter.native="getNoticeLogList('search')">
                
                 <el-form-item label="关键字">
                     <el-input v-model="form.keyword" placeholder="关键字" clearable></el-input>
                </el-form-item>
                <el-form-item label="状态">
                     <el-select v-model="form.status" placeholder="请选择" clearable>
                        <el-option
                        label="待处理"
                        value="1">
                        </el-option>
                        <el-option
                        label="已处理"
                        value="2">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="开始时间">
                     <el-date-picker
                        v-model="form.startTime"
                        type="datetime"
                        placeholder="选择日期时间"
                        align="right"
                        :picker-options="pickerOptions"
                        :format="format"
                        :value-format="format"
                        >
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="结束时间">
                     <el-date-picker
                        v-model="form.endTime"
                        type="datetime"
                        placeholder="选择日期时间"
                        align="right"
                        :picker-options="pickerOptions"
                        :format="format"
                        :value-format="format"
                        >
                    </el-date-picker>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" icon="el-icon-search" @click="getNoticeLogList('search');" :loading="tableLoading">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="message-table">
            <el-table
            ref="record-table"
            :data="list"
            style="width: 100%"
            stripe
            :height="tableHeight"
            highlight-current-row
             v-loading="tableLoading"
            >
                 <!-- <el-table-column
                    type="index"
                    width="50">
                </el-table-column>
                <el-table-column
                label="标题"
                sortable
                prop="notification"
                >
                    
                </el-table-column> -->
                <el-table-column
                label="主机"
                sortable
                prop="hostIp"
                >
                    
                </el-table-column>
                <el-table-column
                label="联系人"
                width="150"
                prop="Contact"
                >
                </el-table-column>
                <el-table-column
                label="告警类型"
                prop="notification"
                >
                    <!-- <template slot-scope="scope">
                        <p v-for="item in warningType" :key="item.typeId">
                         <span v-if="scope.row.type == item.typeId">{{item.name}}</span>
                     </p>
                    </template> -->
                </el-table-column>
                <el-table-column
                prop="info"
                label="告警内容"
                >
                </el-table-column>
                <el-table-column
                label="状态"
                >
                    <template slot-scope="scope">
                        <p v-if="scope.row.status == 1">未推送</p>
                        <p v-else-if="scope.row.status == 2">已推送提醒</p>
                    </template>
                </el-table-column>
                <el-table-column
                prop="updated_at"
                label="更新时间"
                width="200"
                sortable
                fixed="right"
                >
                </el-table-column>
                 <el-table-column
                prop="created_at"
                label="添加时间"
                width="200"
                sortable
                fixed="right"
                >
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
            <el-pagination
               :page-sizes="[10, 20, 50, 100]"
                :page-size="pagenation.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagenation.total"
                :current-page="pagenation.page"
                @size-change='sizeChange'
                @current-change='curreantChange'
                >
            </el-pagination>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                form:{
                    keyword:'',
                    status:'',
                    startTime:'',
                    endTime:'',
                },
                list:null,//表格数据
                pagenation:{
                    page:1,
                    pagesize:10,
                    total:0
                },
                tableHeight:600, // 表格高度
                tableLoading:false,
                pickerOptions: {
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                        picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '昨天',
                        onClick(picker) {
                        const date = new Date();
                        date.setTime(date.getTime() - 3600 * 1000 * 24);
                        picker.$emit('pick', date);
                        }
                    }, {
                        text: '一周前',
                        onClick(picker) {
                        const date = new Date();
                        date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', date);
                        }
                    }]
                },
                format:'yyyy-MM-dd HH:mm:ss',
                //告警类型
                warningType:null
            }
        },
        methods:{
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-50
            },
            getNoticeLogList(type){
                //获取到列表
                 if(type=='search')
                {
                    //是查询
                    this.pagenation.page=1;
                }
                //获取到设备列表
                this.tableLoading = true; //加载中
                var params = {
                    page:this.pagenation.page,
                    pagesize:this.pagenation.pagesize
                }
                params = {...params,...this.form};
                capis.getNoticeLogList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                        //对总数进行赋值
                        this.pagenation.total=data.res.total;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
              sizeChange(pagesize){
                //更改每页的显示条数
                this.pagenation.pagesize = pagesize;
                this.getNoticeLogList();
            },
            curreantChange(page)
            {
                this.pagenation.page = page;
                this.getNoticeLogList();
            },
            getWarnType(){
                this.warningType = ctool.getWarningType();
            }
        },
        created(){
            this.getWarnType();
            this.getNoticeLogList();
            this.$nextTick(()=>{
                this.getHeight();
                window.onresize = () => {
                    this.getHeight();
                };
            })
        }
    }
</script>

<style scoped lang="scss">
    .warning{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        height: 100%;
        .message-top{
            .message-title{
                font-size: 24px;
                font-weight: 900;
            }
            .message-type{
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            }
        }
        .message-pagenation{
            text-align: center;
            padding: 10px 0;
        }
    }
</style>
<style>
.service_dialog{
       border-radius: 15px;
       box-shadow: 0 0 30px 0 rgba(94, 94, 198, 0.7);
       box-sizing: border-box;
   }
</style>


