 * @LastEditTime: 2022-05-23 16:18:09
-->
<template>
  <div class="body">
    <el-row>
      <el-col :span="12">
        <!-- 左侧 -->
        <div class="desk_lo">
          <div class="index_bg_di"></div>
          <img src="../../assets/login/index_desk.png" alt="" width="766" height="663" class="index_bg_desk_img">
          <div class="index_bg_bottom"></div>
        </div>
        
      </el-col>
      <el-col :span="12">
          <!--右侧-->
          <div class="desk_lr">
            <div class="index_bg_rt"></div>
            <div class="login_content">
                <p class="welcom_title">Hi~</p>
                <p class="spa_title">欢迎使用<span v-if="sysParams && sysParams.web_title">{{sysParams.web_title}}</span></p>
                <div class="login_item">
                  <el-form ref="form" :rules="rules" :model="formInline" label-width="80px" hide-required-asterisk @keydown.enter.native="onSubmit('form')">
                    <el-form-item label="用户名" prop="email">
                      <el-input v-model="formInline.email" clearable class="item"></el-input>
                    </el-form-item>
                     <el-form-item label="密　码" prop="password">
                      <el-input v-model="formInline.password" clearable show-password class="item"></el-input>
                    </el-form-item>
                     <!-- <el-form-item label="验证码" prop="code">
                      <el-input v-model="formInline.code" clearable style="width:300px;" class="item"></el-input>
                      <div class="yzmimg"><img :src="yzmSrc" alt=""></div>
                    </el-form-item> -->
                    <el-form-item label-width="30px">
                      <el-button type="primary" round style="width:100%;" @click="onSubmit('form')">登录</el-button>
                    </el-form-item>
                  </el-form>
                </div>
            </div>
            <p class="copyright" v-if="sysParams && sysParams.copy_right">
              {{sysParams.copy_right}}
            </p>
          </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      cTitle:'运维管理系统',
      formInline: {
        email: '',
        password: '',
        code:""
      },
      sysParams:null,
      rules: {
        email: [
          { required: true, message: '请输入账户', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        //  code: [
        //   { required: true, message: '请输入验证码', trigger: 'blur' }
        // ],
      }
    }
  },

  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
          if (valid) {
              var loading = this.$loading({
                lock: true,
                text: 'Loading',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
              })
              var params = {
                email:this.$getRsaCode(this.formInline.email),
                password:this.$getRsaCode(this.formInline.password)
              }
              capis.login(params,{})
              .then(data=>{
                loading.close();
                if(data.status == 'success'){
                  this.$notify({
                    title: '登录成功',
                    message: '欢迎使用智能化系统',
                    type: 'success'
                  });
                  // 设置本地的access_token
                  window.localStorage.setItem('access_token',data.res.original.access_token);
                  // 设置token事件的限制
                  window.localStorage.setItem('expires_in',data.res.original.expires_in);
                  // 设置token类型
                  window.localStorage.setItem('token_type',data.res.original.token_type);
                  // 设置token设置时间戳，已此来判断是否token失效 
                  // 公式：当前时间戳-存储的时间戳《= expires_in*1000 即未失效，否则为已失效
                  window.localStorage.setItem('token_time',new Date().getTime());
                  this.$router.push("/index")  // 路由跳转
                }else{
                  this.$notify({
                    title: '登录失败',
                    message: '账号或密码不正确',
                    type: 'warning'
                  });
                }
                
              })
              .catch(err=>{
                loading.close();
                this.$notify({
                  title: '登录失败',
                  message: '账号或密码不正确',
                  type: 'warning'
                });
                // 删除本地的access_token
                window.localStorage.removeItem('access_token');
                // 删除token事件的限制
                window.localStorage.removeItem('expires_in');
                // 删除token类型
                window.localStorage.removeItem('token_type');
                // 删除token设置时间戳，已此来判断是否token失效 
                // 公式：当前时间戳-存储的时间戳《= expires_in*1000 即未失效，否则为已失效
                window.localStorage.removeItem('token_time');
              })
          } else {
          }
      });
    },
    getSettingListEnable(){
      var params = {}
      capis.getSettingListEnable(params,{})
      .then(data=>{
          if(data.status == 'success'){
              //对数据进行赋值
              this.sysParams = data.res.data;
              localStorage.setItem('sysParams',JSON.stringify(this.sysParams));
          }else{
              this.$notify({
                  title: '提示',
                  message: data.des,
                  type: 'warning'
              });
          }
      })
      .catch(err=>{
      })
    }
  },
  created() {
    //获取到系统参数
    this.getSettingListEnable();
    var title = this.$route.params.title;
    var message = this.$route.params.message;
    var type = this.$route.params.type;
    if(title && message && type) {
        this.$notify({
            title: title,
            message: message,
            type: type
        });
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<!-- 添加“scoped”属性以将CSS限制到此组件 -->
<style scoped lang="scss">
    .desk_lo
    {
      height: 100vh;
      background-color: #EDF6FD;
      display: flex;
      justify-content: center; /*左右居中 */
      align-items: center;/*上下居中 */
      position: relative;
      overflow: hidden;
      .index_bg_desk_img{
        z-index: 2;    
      }
      .index_bg_di{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url(../../assets/login/index_desk_di.png) no-repeat fixed center;
        background-size:contain;
        background-position: 0 -90px;
      }
      .index_bg_bottom{
        position: absolute;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 219px;
        background: url(../../assets/login/index_desk_bottom.png) no-repeat;
        background-size:contain;
        background-size:960px 219px;
      }
    }
    .desk_lr{
        position: relative;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        .index_bg_rt{
          position: absolute;
          top: 0;
          right: 0;
          width: 100%;
          height: 100%;
          background: url(../../assets/login/index_desk_tr.png) no-repeat;
          background-size:contain;
          background-position: 621px -133px;
        }
        .login_content{
          width: 488px;
         
          .welcom_title{
            font-size: 46px;
            font-weight: bold;
            color: #000;
            height: 60px;
            line-height: 60px;
          }
          .spa_title{
            font-size: 34px;
            font-weight: bold;
            line-height: 54px;
            padding: 20px 0 52px 0;
            span{
              color: #5E5EC6;
            }
          }
          .login_item{
            .yzmimg{
                float: right;
                height: 40px;
                width: 100px;
                position: relative;
                border-radius: 4px;
                overflow: hidden;
                background-color: #5E5EC6;
            }
            input{
              border: none!important;
            }
            .el-button--primary{
              background-color: #5e5ec6;
              border-color: #5e5ec6;
            }
            .el-form-item__label{
              color:#999;
            }
          }
        }
        .copyright{
          position: absolute;
          bottom: 40px;
          font-size: 16px;
          color: #d6d6d6;
        }
    }
</style>



