 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-24 15:06:58
-->
<template>
    <div class="warning">
        <div class="message-top" ref="message-top">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item>
                    <el-button icon="el-icon-circle-plus-outline" type="success" @click="serviceShow = !serviceShow">新增</el-button>
                    <el-button icon="el-icon-delete" type="danger">删除</el-button>
                    <el-button icon="el-icon-folder-opened" type="warning">导入</el-button>
                    <el-button icon="el-icon-download" type="warning">导出</el-button>
                </el-form-item>
                <el-form-item>
                        <el-input v-model="input" placeholder="请输入内容" clearable></el-input>
                </el-form-item>
                <el-form-item>
                        <el-button icon="el-icon-search" type="primary">查询</el-button>
                </el-form-item>
            </el-form>
            
            <el-col :span="12">
               
            </el-col>
            
        </div>
        <div class="message-table">
            <el-table
            ref="record-table"
            :data="messageJson"
            style="width: 100%"
            stripe
            :height="tableHeight"
            highlight-current-row
            >
                <el-table-column
                    type="selection"
                    width="55">
                </el-table-column>
                 <el-table-column
                    type="index"
                    width="50">
                </el-table-column>
                <el-table-column
                label="国家"
                sortable
                prop="country"
                >
                    
                </el-table-column>
                <el-table-column
                label="省"
                width="150"
                sortable
                prop="province"
                >
                
                </el-table-column>
                <el-table-column
                prop="city"
                label="市"
                sortable
                >
                </el-table-column>
                <el-table-column
                prop="start_ip"
                label="起始IP"
                sortable
                >
                </el-table-column>
                <el-table-column
                prop="end_ip"
                label="结束IP"
                sortable
                >
                </el-table-column>
                <el-table-column
                label="操作"
                sortable
                width="300"
                >
                 <template slot-scope="scope">
                    <el-button
                    @click.native.prevent="deleteRow(scope.$index, tableData)"
                    size="mini"
                    type="primary" icon="el-icon-edit"
                    >
                    编辑
                    </el-button>
                     <el-button
                        size="mini"
                     icon="el-icon-delete"
                     type="danger">
                    删除
                    </el-button>
                     <el-button
                        size="mini"
                        icon="el-icon-error"
                        type="danger">
                    黑名单
                    </el-button>
                </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
            <el-pagination
                :page-sizes="[100, 200, 300, 400]"
                :page-size="100"
                layout="total, sizes, prev, pager, next, jumper"
                :total="400">
            </el-pagination>
        </div>
       <el-dialog width="50%" :visible.sync="serviceShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog">
            <WordAdd></WordAdd>
        </el-dialog>
    </div>
</template>
<script>
    import WordAdd from './add/add.vue'
    export default{
        data(){
            return{
                
                messageJson:[
                    {
                        province:'河南省',
                        city:"郑州市",
                        start_ip:'192.168.1.1',
                        end_ip:'192.168.1.1',
                        country:'中国'
                    }
                ],//表格数据
                tableHeight:600, // 表格高度
                serviceShow:false
            }
        },
        methods:{
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-50
            }
        },
        mounted(){
            this.getHeight();
        },
        components:{
            WordAdd
        },
        computed:{
            
        }
    }
</script>

<style scoped lang="scss">
    .warning{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        height: 100%;
        .message-top{
            .message-title{
                font-size: 24px;
                font-weight: 900;
            }
            .message-type{
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            }
        }
        .message-pagenation{
            text-align: center;
            padding: 10px 0;
        }
    }
</style>
<style>
.service_dialog{
       border-radius: 15px;
       box-shadow: 0 0 30px 0 rgba(94, 94, 198, 0.7);
       box-sizing: border-box;
   }
</style>


