<template>
    <el-container class="container">
        <el-aside width="240px"> <!-- class="hidden-lg-and-down" -->
            <!-- 左侧的当行 -->
            <Aside></Aside>
        </el-aside>
        <el-container>
            <el-header height="70px"> <!-- class="hidden-lg-and-down" -->
                <Header></Header>
            </el-header>
            <el-main>
                <div class="breadcrumb">
                    <el-breadcrumb separator="/">
                        <el-breadcrumb-item v-for="(item,index) in broad[route]" :key="index">{{item.name}}</el-breadcrumb-item>
                    </el-breadcrumb>        
                </div>
                <router-view></router-view>
            </el-main>
        </el-container>
    </el-container>
</template>
<script>
import Aside from './aside/aside'
import Header from './header/header'

// 订阅扫码登录事件

export default{
    data(){
        return{
            route:'',
            broad:
            {
                '/index/welcom':[
                    {
                        name:'首页'
                    }
                ],
                '/index/services':
                [
                    {
                        name:'状态监控'
                    },
                    {
                        name:'设备列表'
                    }
                ],
                '/index/warninglog':
                [
                    {
                        name:'状态监控'
                    },
                    {
                        name:'告警日志'
                    }
                ],
                 '/index/devices':
                [
                    {
                        name:'告警设置'
                    },
                    {
                        name:'设备管理'
                    }
                ],
                 '/index/warning':
                [
                    {
                        name:'告警设置'
                    },
                    {
                        name:'告警策略'
                    }
                ],
                 '/index/custom':
                [
                    {
                        name:'告警设置'
                    },
                    {
                        name:'人员管理'
                    }
                ],
                 '/index/word':
                [
                    {
                        name:'告警设置'
                    },
                    {
                        name:'字冠管理'
                    }
                ],
                 '/index/firewall':
                [
                    {
                        name:'告警设置'
                    },
                    {
                        name:'防火墙'
                    }
                ],
                 '/index/params':
                [
                    {
                        name:'告警设置'
                    },
                    {
                        name:'系统参数'
                    }
                ],
                '/index/services/servicesinfo':
                [
                    {
                        name:'状态监控',
                    },
                    {
                        name:'设备列表',
                    },
                    {
                        name:'设备信息',
                    },
                ]
            }
        }
    },
    created(){
        this.getBreadcrumd();
    },
    components:{
       Aside,
       Header
    },
    methods:{
        getBreadcrumd(){
            //获取到面包屑
            this.route = this.$route.path
        }
    },
    watch:{
        $route(){
            this.getBreadcrumd();
        }
    }
}
</script>
<style scoped lang="scss">
    .container{
        height: 100vh;
    }
</style>
<style>
    .el-header{
        position: relative;
        padding:  0 0px;
    }
    .breadcrumb{
        padding:  0 0 5px 0;
    }
    .el-main
    {
        padding-top: 10px;
    }
</style>



