import Vue from 'vue'
import Router from 'vue-router'
//引入页面
//import 引用名 form '@/路径'
import Login from '@/components/login/login'   //登录页面
import Index from '@/components/index/index'   //首页
import Welcome from '@/components/welcome/welcome'   //主页
import Welcome2 from '@/components/welcome2/welcome2'   //主页2
import Message from '@/components/message/message'   //消息
import Warning from '@/components/warning/warning'   //告警与报错
import Warninglog from '@/components/warning/warninglog'   //告警日志
import Devices from '@/components/devices/devices'   //设备管理
import Services from '@/components/servicesmanage/services'   //设备管理
import Servicesinfo from '@/components/servicesmanage/servicesinfo/serviceinfo'   //设备管理
import Warninfo from '@/components/servicesmanage/warninfo/warninfo'   //单个设备详情
import Errorlog from '@/components/servicesmanage/warninfo/items/errorlog'   //单个设备详情错误日志
import Faillog from '@/components/servicesmanage/warninfo/items/faillog'   //单个设备详情失败日志
import Connectnumber from '@/components/servicesmanage/warninfo/items/connectnumber'   //单个设备详情连接数
import Cpuuse from '@/components/servicesmanage/warninfo/items/cpuuse'   //单个设备详情cpu使用情况
import Connectfail from '@/components/servicesmanage/warninfo/items/connectfail'   //单个设备详情cpu使用情况
import Custom from '@/components/custom/custom' // 人员管理
import Word from '@/components/word/word' // 字冠设置
import Firewall from '@/components/firewall/firewall' // 防火墙
import Params from '@/components/sysparms/sysparams' // 系统参数


//解决重复路由报错问题
const originalReplace = Router.prototype.replace
Router.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch(err => err)
}
let routerPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(err => err)
}
Vue.use(Router)
//注册路由
export default new Router({
    // mode:'history',
    routes: [
        {
            path: '/', //路径
            name: 'Login', //渲染位置
            component: Login,  //引用名
            meta: { title:'智能化管理系统', keyword:'', description:'', },
        },
        {
            path: '/index', //路径
            name: 'Index', //渲染位置
            component: Index,  //引用名
            meta: { title:'首页', keyword:'', description:'', },
            redirect:'/index/welcom',
            children:[
                {
                    path: '/index/welcom', //路径
                    name: 'Welcome', //渲染位置
                    component: Welcome2,  //引用名
                    meta: { title:'首页', keyword:'', description:'', },
                },
                {
                    path: '/index/message', //路径
                    name: 'message', //渲染位置
                    component: Message,  //引用名
                    meta: { title:'消息', keyword:'', description:'', },
                },
                {
                    path: '/index/warning', //路径
                    name: 'warning', //渲染位置
                    component: Warning,  //引用名
                    meta: { title:'告警策略', keyword:'', description:'', },
                },
                {
                    path: '/index/warninglog', //路径
                    name: 'warninglog', //渲染位置
                    component: Warninglog,  //引用名
                    meta: { title:'告警日志', keyword:'', description:'', },
                },
                {
                    path: '/index/devices', //路径
                    name: 'devices', //渲染位置
                    component: Devices,  //引用名
                    meta: { title:'设备管理', keyword:'', description:'', },
                },
                {
                    path: '/index/services', //路径
                    name: 'services', //渲染位置
                    component: Services,  //引用名
                    meta: { title:'设备列表', keyword:'', description:'', },
                },
                {
                    path: '/index/services/servicesinfo', //路径
                    name: 'servicesinfo', //渲染位置
                    component: Servicesinfo,  //引用名
                    meta: { title:'设备信息', keyword:'', description:'', },
                },
                {
                    path: '/index/warninfo', //路径
                    name: 'warninfo', //渲染位置
                    component: Warninfo,  //引用名
                    meta: { title:'设备信息', keyword:'', description:'', },
                    redirect:'/index/warninfo/errorlog',
                    children:[
                        {
                            path: '/index/warninfo/errorlog', //路径
                            name: 'errorlog', //渲染位置
                            component: Errorlog,  //引用名
                            meta: { title:'错误日志', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/faillog', //路径
                            name: 'faillog', //渲染位置
                            component: Faillog,  //引用名
                            meta: { title:'失败日志', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/connectnumber', //路径
                            name: 'connectnumber', //渲染位置
                            component: Connectnumber,  //引用名
                            meta: { title:'连接数', keyword:'', description:'', },
                        },
                        {
                            path: '/index/warninfo/cpuuse', //路径
                            name: 'cpuuse', //渲染位置
                            component: Cpuuse,  //引用名
                            meta: { title:'CPU使用情况', keyword:'', description:'', },
                        }
                        ,
                        {
                            path: '/index/warninfo/connectfail', //路径
                            name: 'connectfail', //渲染位置
                            component: Connectfail,  //引用名
                            meta: { title:'连接失败数', keyword:'', description:'', },
                        }
                    ]
                },
                {
                    path: '/index/custom', //路径
                    name: 'custom', //渲染位置
                    component: Custom,  //引用名
                    meta: { title:'人员管理', keyword:'', description:'', },
                },
                {
                    path: '/index/word', //路径
                    name: 'Word', //渲染位置
                    component: Word,  //引用名
                    meta: { title:'字冠设置', keyword:'', description:'', },
                },
                {
                    path: '/index/firewall', //路径
                    name: 'firewall', //渲染位置
                    component: Firewall,  //引用名
                    meta: { title:'防火墙', keyword:'', description:'', },
                },
                {
                    path: '/index/params', //路径
                    name: 'params', //渲染位置
                    component: Params,  //引用名
                    meta: { title:'系统参数', keyword:'', description:'', },
                },
            ]
        },
    ],
})



