<template>
   <div class="warning-add">
       <p class="warning-add-title">基础选项</p>
       <el-form ref="form" :model="form" :rules="rules" label-width="100px">
            <el-form-item label="国家：" prop="title">
                <el-input v-model="form.title" clearable></el-input>
            </el-form-item>
           <el-form-item label="省：" prop="title">
                <el-input v-model="form.title" clearable></el-input>
            </el-form-item>
            <el-form-item label="市：" prop="title">
                <el-input v-model="form.title" clearable></el-input>
            </el-form-item>
            <el-form-item label="起始IP：" prop="title">
                <el-input v-model="form.title" clearable></el-input>
            </el-form-item>
            <el-form-item label="结束IP：" prop="title">
                <el-input v-model="form.title" clearable></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('form')">增加</el-button>
                <el-button>取消</el-button>
            </el-form-item>
        </el-form>
   </div>
</template>
<script>
    export default{
        data(){
           
            return{
                form:{
                    title:'',
                    type:'1',
                    phone:'',
                    email:'',
                    content:'',
                    status:'',
                    delivery:''
                },
                 rules: {
                    title: [
                        { required: true, message: '请输入名称', trigger: 'blur' },
                    ],
                }
            }
        },
        components:{
        },
        methods: {
          
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
            },
        }
    }
</script>
<style scoped lang="scss">
   .warning-add{
       padding: 0 80px;
       .warning-add-title{
           font-size: 28px;
           font-weight: 600;
           height: 60px;
           line-height: 60px;
       }
   }
</style>


