<template>
    <div class="message" ref="message">
        <div class="message-top" ref="message-top">
            <p class="message-title">告警与报错</p>
           
        </div>
        <div class="message-table">
            <el-table
            ref="record-table"
            :data="list"
            style="width: 100%"
            stripe
            highlight-current-row
            >
                <el-table-column
                label="详情"
                sortable
                >
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.info }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                label="设别名称或者ip"
                width="150"
                sortable
                >
                 <template slot-scope="scope">
                    <p>{{ scope.row.host }}</p>
                </template>
                </el-table-column>
                <el-table-column
                prop="updated_at"
                label="时间"
                width="200"
                sortable
                fixed="right"
                >
                </el-table-column>
            </el-table>
        </div>
       
    </div>
</template>
<script>
    export default{
        data(){
            return{
               
                list:null,//表格数据
            }
        },
        methods:{
           
             getNoticeLogList(){
               
                //获取到设备列表
                this.tableLoading = true; //加载中
                var params = {
                    page:1,
                    pagesize:10,
                    status:1,
                    searchType:1
                }
                capis.getNoticeLogList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
        },
        mounted(){
            this.getNoticeLogList();
        }
    }
</script>

<style scoped lang="scss">
    .message{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        height: 100%;
        .message-top{
            display: flex;
            justify-content: space-between;
            .message-title{
                font-size: 24px;
                font-weight: 900;
            }
            .message-type{
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            }
        }
        .message-pagenation{
            text-align: center;
            padding: 10px 0;
        }
    }
</style>


