 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-23 15:55:01
-->
<template>
    <div class="content-box x4">
        <p class="content-box-title">资源预警</p>
        <div class="item-box">
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">近24小时告警数</p>
                </div>
                <el-tooltip placement="top">
                    <div slot="content" v-if="list && list.now">总 数：{{list.now.count}}<br/>已完成：{{list.now.complete}}<br/>未完成：{{list.now.incomplete}}</div>
                    <p class="item-top-data" v-if="list && list.now"><span class="data1">{{list.now.count}}</span>/<span class="data2">{{list.now.complete}}</span>/<span class="data3">{{list.now.incomplete}}</span></p>
                </el-tooltip>
            </div>
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">近一周告警数</p>
                </div>
                <el-tooltip placement="top">
                    <div slot="content" v-if="list && list.now">总 数：{{list.week.count}}<br/>已完成：{{list.week.complete}}<br/>未完成：{{list.week.incomplete}}</div>
                    <p class="item-top-data" v-if="list && list.now"><span class="data1">{{list.week.count}}</span>/<span class="data2">{{list.week.complete}}</span>/<span class="data3">{{list.week.incomplete}}</span></p>
                </el-tooltip>
            </div>
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">近一月告警数</p>
                </div>
                <el-tooltip placement="top">
                    <div slot="content" v-if="list && list.now">总 数：{{list.month.count}}<br/>已完成：{{list.month.complete}}<br/>未完成：{{list.month.incomplete}}</div>
                    <p class="item-top-data" v-if="list && list.now"><span class="data1">{{list.month.count}}</span>/<span class="data2">{{list.month.complete}}</span>/<span class="data3">{{list.month.incomplete}}</span></p>
                </el-tooltip>
            </div>
            <!-- <div class="item">
                <div class="item-top">
                    <p class="item-top-name">近24小时告警数</p>
                </div>
                <p class="item-top-data"><span class="data1">18</span>/<span class="data2">9</span>/<span class="data3">9</span></p>
            </div>
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">近一周告警数</p>
                </div>
                <p class="item-top-data"><span class="data1">18</span>/<span class="data2">9</span>/<span class="data3">9</span></p>
            </div>
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">近一月告警数</p>
                </div>
                <p class="item-top-data"><span class="data1">18</span>/<span class="data2">9</span>/<span class="data3">9</span></p>
            </div> -->
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                list: {
                    "now":{
                        "count":0,
                        "complete":0,
                        "incomplete":0
                    },
                    "week":{
                        "count":0,
                        "complete":0,
                        "incomplete":0
                    },
                    "month":{
                        "count":0,
                        "complete":0,
                        "incomplete":0

                    }
                }
            }
        },
        created(){
            this.getResourseWarning();
        },
        methods:{
            //获取到详情
            getResourseWarning(){
                var params = {}
                capis.getResourseWarning(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            }
        },
        components:{
        }
    }
</script>
<style scoped lang="scss">
   
    .content-box{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        padding: 20px 40px;
        border-radius: 20px;
        margin-bottom: 20px;
        .content-box-title{
            font-size: 14px;
            color: #666;
            margin-bottom: 20px;
        }
        .item-box{
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            .item{
                    box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
                    padding: 10px 20px;
                    border-radius: 10px;
                    margin-bottom: 20px;
                    margin-right: 20px;
                .item-top{
                    display: flex;
                    justify-content: space-between;
                    .item-top-name{
                        font-size: 12px;
                        color: #999;
                    }
                    .item-top-icon{
                        font-size: 18px;
                    }
                }
                .item-top-data{
                    font-size: 18px;
                    margin-top: 10px;
                    color: #999;
                    .data1{
                        font-size: 26px;
                        color: #000;
                    }
                    .data2{
                        font-size: 20px;
                        color: #95d871;
                    }
                    .data3{
                        font-size: 20px;
                        color: #ff3838;
                    }
                }
            }
        }
    }
    .content-box.x4{
        width: 40%;
        .item-box{
            .item{
                width: 22%;
            }
        }
    }
    
   
</style>


