<!--
 * @Descripttion: your project
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 23:06:31
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-25 18:13:47
-->
<template>
    <div class="aside_content">
        <div class="login_tiltle">
            <div>
                <img src="../../../assets/index/logo.png" alt="">
            </div>
            <div>
                <el-tooltip class="item" :content="sysParams.web_title" placement="top-start">
                    <span>{{webTitle}}</span>
                </el-tooltip>
            </div>
        </div>
        <div class="menu">
             <el-menu 
                class="el-menu-vertical-demo"
                background-color="#5e5ec6"
                text-color="#fff"
                active-text-color="#e9cd5e"
                :router="true"
                :default-active="defaultActive"
                :unique-opened="true"
             >
                <div v-for="(item,index) in menus" :key="index">
                    <!-- 只有一级 -->
                    <el-menu-item :index="item.url" v-if="item.url">
                        <i :class="eitem.icon"></i>
                        <span slot="title">{{item.name}}</span>
                    </el-menu-item>
                    <!-- 含有多级 -->
                    <el-submenu v-if="!item.url" :index='item.name'>
                        <template slot="title">
                            <i :class="item.icon"></i>
                            <span>{{item.name}}</span>
                        </template>
                        <el-menu-item-group>
                            <el-menu-item v-for="(it,ind) in item.children" :key="ind" :index="it.url">
                                <i :class="it.icon"></i>
                                <span slot="title">{{it.name}}</span>
                            </el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                </div>
            </el-menu>
        </div>
    </div>
</template>
<script>

export default{
    data(){
        return{
            defaultActive:'',  //激活的菜单
            sysParams:null,
            menus:[
                {
                    "name":"状态监控",
                    "url":"",
                    "icon":"el-icon-warning-outline",
                    "children":[
                        {
                            "name":"设备列表",
                            "url":"/index/services",
                            "icon":"el-icon-s-grid",
                            "children":[]
                        },
                        {
                            "name":"告警日志",
                            "url":"/index/warninglog",
                            "icon":"el-icon-s-order",
                            "children":[]
                        }
                    ]
                },
                {
                    "name":"常用设置",
                    "url":"",
                    "icon":"el-icon-setting",
                    "children":[
                        {
                            "name":"设备管理",
                            "url":"/index/devices",
                            "icon":"el-icon-s-platform",
                            "children":[]
                        },
                        {
                            "name":"告警策略",
                            "url":"/index/warning",
                            "icon":"el-icon-s-marketing",
                            "children":[]
                        },
                        {
                            "name":"人员管理",
                            "url":"/index/custom",
                            "icon":"el-icon-s-custom",
                            "children":[]
                        }
                    ]
                },
                {
                    "name":"高级设置",
                    "url":"",
                    "icon":"el-icon-setting",
                    "children":[
                        {
                            "name":"防火墙",
                            "url":"/index/firewall",
                            "icon":"el-icon-s-ticket",
                            "children":[]
                        },
                        {
                            "name":"系统参数",
                            "url":"/index/params",
                            "icon":"el-icon-s-ticket",
                            "children":[]
                        },
                        

                    ]
                }
            ]
        }
    },
    methods:{
        getRoutePath(to,from){
            for(var i=0; i<this.menus.length; i++)
            {
                if(this.menus[i].url && to.path.indexOf(this.menus[i].url) != -1)
                {
                    this.defaultActive = this.menus[i].url;
                    break;
                }
                // if(this.menus[i].children && this.menus[i].children.length>0)
                // {
                //     for(var j=0; j<this.menus[i].children.length; j++)
                //     {
                //          if(this.menus[i].children[j].url && to.path.indexOf(this.menus[i].children[j].url) != -1)
                //         {
                //             this.defaultActive = this.menus[i].children[j].url;
                //             break;
                //         }
                //     }
                // }
            }
        },
        getRoute(){
            for(var i=0; i<this.menus.length; i++)
            {
                if(this.menus[i].url && this.$route.path.indexOf(this.menus[i].url) != -1)
                {
                    this.defaultActive = this.menus[i].url;
                    break;
                }
                if(this.menus[i].children && this.menus[i].children.length>0)
                {
                    for(var j=0; j<this.menus[i].children.length; j++)
                    {
                        if(this.menus[i].children[j].url && this.$route.path.indexOf(this.menus[i].children[j].url) != -1)
                        {
                            this.defaultActive = this.menus[i].children[j].url;
                            console.log(159)
                            break;
                        }
                    }
                }
            }
        },
        
    },
    computed:{
        webTitle(){
            return this.sysParams.web_title.slice(0,6);
        }
    },
    watch:{
        '$route':'getRoutePath'
    },
    created(){
        
        this.sysParams = JSON.parse(localStorage.getItem('sysParams'));
        this.$nextTick(()=>{
            this.getRoute();
        })
    }
}
</script>
<style scoped lang="scss">
    .aside_content{
        background-color: #5e5ec6;
        height: 100%;
        border-radius: 0 50px 0 0;
        .login_tiltle{
            height: 120px;
            display: flex;
            justify-content: center;
            align-items: center;
            span{
                color: #ffffff;
                font-size: 24px;
                margin-left: 8px;
            }
        }
        .el-menu{
            border: none;
            i{
                color:#fff;
            }
            .el-menu-item.is-active{
                i{
                    color: #e9cd5e;
                }
               
            }
        }
    }
</style>


