<template>
   <div class="notice_info">
       <p class="title">最近一周报警信息</p>
       <!-- 折线图 -->
       <div class="" id="myChart" :style="{ height: '400px','margin-bottom':'20px'}"></div>
   </div>
</template>
<script>
    export default{
        data(){
            return{
                echarts:null,
            }
        },
        methods:{
            initEchats(){
                var option = {
                    xAxis: {
                        type: 'category',
                        data: ['3-21', '3-22', '3-23', '3-24', '3-25', '3-26', '3-27'],
                        axisPointer: {
                            lineStyle: {
                                color: '#5e5ec6', //提示线颜色
                                width: 2
                            },
                        },
                    },
                    yAxis: {
                        type: 'value'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    toolbox: {
                        feature: {
                        saveAsImage: {}
                        }
                    },
                    grid:{
                        x:50, //左侧未知
                        // y:0
                        x2:0 //右侧
                    },

                    series: [
                        {
                        data: [0, 932, 901, 934, 1290, 1330, 0],
                        type: 'line',
                        smooth: true,
                        itemStyle:{
                            color:'#5e5ec6' //线的颜色
                        },
                        areaStyle:{
                            normal:{
                                color:new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                    {
                                        offset: 0,
                                        color: 'rgba(93,115,199,0.5)' //渐变上
                                    },
                                    {
                                        offset: 1,
                                        color: 'rgba(93,115,199,0)' //渐变下
                                    }
                                ]
                            )}
                            }
                        }
                    ]
                };
                this.echarts = this.$echarts.init(document.getElementById('myChart'));
                this.echarts.setOption(option);
            }
        },
        mounted(){
            this.initEchats();
        },
        components:{
        }
    }
</script>
<style scoped lang="scss">
   .notice_info{
       padding: 40px 40px;
       height: 400px;
       .title{
            font-size: 18px;
            font-weight: 600;
       }
   }
</style>


