<!--
 * @Descripttion: 首页-资源预览
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 23:06:31
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-23 15:53:44
-->
<template>
    <div class="content-box x6">
        <p class="content-box-title">资源预览</p>
        <div class="item-box">
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">主机总数</p>
                    <p class="item-top-icon"><i class="el-icon-s-data" style="color:#5e5ec6"></i></p>
                </div>
                <p class="item-top-data" v-if="list && list.host">{{list.host.count}}</p>
            </div>
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">在线数</p>
                    <p class="item-top-icon"><i class="el-icon-document-checked" style="color:#9fdc7e"></i></p>
                </div>
                <p class="item-top-data" v-if="list && list.host">{{list.host.onlineCount}}</p>
            </div>
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">离线数</p>
                    <p class="item-top-icon"><i class="el-icon-document-delete" style="color:#ff3838"></i></p>
                </div>
                <p class="item-top-data" v-if="list && list.host">{{list.host.offlineCount}}</p>
            </div>
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">告警数</p>
                    <p class="item-top-icon"><i class="el-icon-bell" style="color:#fed362"></i></p>
                </div>
                <p class="item-top-data" v-if="list && list.host">{{list.host.errCount}}</p>
            </div>
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">服务总数</p>
                    <p class="item-top-icon"><i class="el-icon-monitor" style="color:#5e5ec6"></i></p>
                </div>
                <p class="item-top-data" v-if="list && list.host">{{list.server.count}}</p>
            </div>
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">运行数</p>
                    <p class="item-top-icon"><i class="el-icon-video-play" style="color:#9fdc7e"></i></p>
                </div>
                <p class="item-top-data" v-if="list && list.host">{{list.server.onlineCount}}</p>
            </div>
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">停止数</p>
                    <p class="item-top-icon"><i class="el-icon-video-pause" style="color:#ff3838"></i></p>
                </div>
                <p class="item-top-data" v-if="list && list.host">{{list.server.offlineCount}}</p>
            </div> 
            <div class="item">
                <div class="item-top">
                    <p class="item-top-name">告警数</p>
                    <p class="item-top-icon"><i class="el-icon-bell" style="color:#fed362"></i></p>
                </div>
                <p class="item-top-data" v-if="list && list.host">{{list.server.errCount}}</p>
            </div> 
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                list:{
                    host:null,
                    server:null
                }
            }
        },
        created(){
            this.getResourseView();
            this.$nextTick(()=>{
                this.initWs();
            })
        },
        methods:{
            //获取到详情
            getResourseView(){
                var params = {}
                capis.getResourseView(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
           initWs(){
                //检测主机状态
                this.$LaravelEcho.instance.channel('HostStatus').listen('HostEvent', (e) => {
                    if(e.type=="HostAdd" || e.type=="HostDel" || e.type=="HostChange")
                    {
                        this.getResourseView();
                    }
                })
            }
        },
        components:{
            
        },
        beforeDestroy(){
            console.log("退出监听通道:HostStatus");
            this.$LaravelEcho.instance.leaveChannel('HostStatus');
        }
    }
</script>
<style scoped lang="scss">
    .content-box{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        padding: 20px 40px;
        border-radius: 20px;
        margin-bottom: 20px;
        .content-box-title{
            font-size: 14px;
            color: #666;
            margin-bottom: 20px;
        }
        .item-box{
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            .item{
                    box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
                    padding: 10px 20px;
                    border-radius: 10px;
                    margin-bottom: 20px;
                    margin-right: 20px;
                .item-top{
                    display: flex;
                    justify-content: space-between;
                    .item-top-name{
                        font-size: 12px;
                        color: #999;
                    }
                    .item-top-icon{
                        font-size: 18px;
                    }
                }
                .item-top-data{
                    font-size: 18px;
                    margin-top: 10px;
                }
            }
        }
    }
    .content-box.x6{
        width: 59%;
        // margin-right: 20px;
        .item-box{
            .item{
                width: 18%;
            }
        }
    }
</style>


