import Echo from 'laravel-echo'
import Pusher from "pusher-js"


/**
 * pusher配置
 */

const pusherConfig = {
    forceTLS: document.location.protocol === 'https:' ? true : false,
    wsHost: document.location.hostname,
    wssHost: document.location.hostname,
    wsPort: 8804,
    wssPort: 8804,
    disableStats: true,
    auth: {
        headers: {
            "Authorization": localStorage.getItem('token_type')+" "+localStorage.getItem('access_token')
        }
    }
    // enabledTransports: ['ws', 'wss'],
}

class LaravelEcho {
    static instance = null

    constructor() {
        this.instance = new Echo({
            client: new Pusher('jingu_eoms', pusherConfig),
            broadcaster: 'pusher'
        })
    }
    setToken() {
        // var token_type = localStorage.getItem('token_type');
        // var access_token = localStorage.getItem('access_token');
        // this.instance.options.client.config.auth.headers.Authorization = `${token_type} ${access_token}`
    }

    removeToken() {
        // this.instance.options.client.config.auth.headers = {}
    }
}

export default new LaravelEcho()


