<!--
 * @Descripttion: 设备列表
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-04-24 16:59:37
-->
<template>
   <div >
       <div>
            <el-table
            ref="record-table"
            :data="messageJson"
            style="width: 100%"
            stripe
            highlight-current-row
            >
                <el-table-column
                label="详情"
                sortable
                >
                    <template slot-scope="scope">
                        <span style="display:inline-block;width:12px;height:12px; border-radius:50%;" :style="[{backgroundColor:scope.row.color}]"></span>
                        <span style="margin-left: 10px">{{ scope.row.content }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                label="设别名称或者ip"
                width="150"
                sortable
                >
                 <template slot-scope="scope">
                    <p>{{ scope.row.name }}</p>
                    <p>{{ scope.row.ip }}</p>
                </template>
                </el-table-column>
                <el-table-column
                prop="date"
                label="时间"
                width="200"
                sortable
                fixed="right"
                >
                </el-table-column>
            </el-table>
       </div>
        <div class="message-pagenation" ref="message-pagenation">
            <el-pagination
                :page-sizes="[100, 200, 300, 400]"
                :page-size="100"
                layout="total, sizes, prev, pager, next, jumper"
                :total="400">
            </el-pagination>
        </div>
   </div>
</template>
<script>
    export default{
        data(){
            return{
               activeIndex:1,
                messageJson:[
                    {
                        content:'警告;检查硬盘分区_vda1;危险等级;高;硬盘警告;/vda1/lujing/lujing; 已使用20G(20%/100%)',
                        type:"warning",
                        ip:'192.168.31.7',
                        name:'CTI服务器',
                        date:"2022-04-01 00:00:00",
                        color:"red",
                    }
                ],//表格数据
            }
        },
        methods:{
           
        },
        mounted(){
        },
        components:{
        },
        computed:{
            
        }
    }
</script>

<style scoped lang="scss">
   .message-pagenation
   {
       padding: 10px 0;
       text-align: center;
   }
</style>


