<!--
 * @Descripttion: 人员管理
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 10:14:14
-->
<template>
    <div class="warning">
        <div class="message-top" ref="message-top">
             <el-form :inline="true" :model="searchForm" class="demo-form-inline" @keydown.enter.native="getContactList('search')">
                <el-form-item label="姓名">
                    <el-input v-model="searchForm.name" placeholder="请输入" clearable></el-input>
                </el-form-item>
                 <el-form-item label="手机号">
                    <el-input v-model="searchForm.phone" placeholder="请输入" clearable></el-input>
                </el-form-item>
                <el-form-item label="状态">
                    <el-select v-model="searchForm.status" placeholder="请选择" clearable>
                        <el-option label="启用" value="1"></el-option>
                        <el-option label="未启用" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" icon="el-icon-search" @click="getContactList('search')" :loading="tableLoading">查询</el-button>
                    <el-button icon="el-icon-circle-plus-outline" type="success" @click="editId='';serviceShow = !serviceShow">新增</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="message-table">
            <el-table
            ref="record-table"
            :data="list"
            style="width: 100%"
            stripe
            :height="tableHeight"
            highlight-current-row
            v-loading="tableLoading"
            >
                <el-table-column
                    type="index"
                    width="50">
                </el-table-column>
                <el-table-column
                label="名称"
                sortable
                prop="name"
                >
                </el-table-column>
                <el-table-column
                label="电话号码"
                sortable
                prop="phone"
                >
                </el-table-column>
                <el-table-column
                prop="email"
                label="邮箱"
                sortable
                >
                </el-table-column>
                <el-table-column
                label="状态"
                >
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.status"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            :active-value='1'
                            :inactive-value='2'
                            @change="changeStatus(scope)"
                            >
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column
                label="操作"
                fixed="right"
                width="200"
                >
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="editInfo(scope);">编辑</el-button>
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteCustom(scope);">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
            <el-pagination
                :page-sizes="[10, 20, 50, 100]"
                :page-size="pagenation.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagenation.total"
                :current-page="pagenation.page"
                @size-change='sizeChange'
                @current-change='curreantChange'
                >
            </el-pagination>
        </div>
        <el-dialog width="50%" :visible.sync="serviceShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <CustomAdd v-on:freshData="freshData"></CustomAdd>
        </el-dialog>
        <el-dialog width="50%" :visible.sync="serviceEditShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog" :destroy-on-close="true">
            <CustomEdit v-on:freshData="freshData" v-if="serviceEditShow" :editId="editId"></CustomEdit>
        </el-dialog>
    </div>
</template>
<script>
    import CustomAdd from './add/add.vue'
    import CustomEdit from './add/edit.vue'
    export default{
        data(){
            return{
                searchForm:{
                    name:'',
                    phone:'',
                    status:''
                },
                list:null,//表格数据
                 pagenation:{
                    page:1,
                    pagesize:10,
                    total:0
                },
                tableHeight:600, // 表格高度
                serviceShow:false,
                serviceEditShow:false, //编辑的显示与不显示
                tableLoading:false,
                editId:''
            }
        },
        methods:{
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-50
            },
            freshData(data)
            {
                // 子组件给父组件传的至
                if(data && data.type == 'cancel')
                {
                    // 子组件取消操作,隐藏弹出层
                    
                }
                else if(data && data.type == 'update')
                {
                    //子组件点击了确认操作， 编辑的更新操作
                    this.getContactList();
                }
                else if(data && data.type == 'add')
                {
                    //子组件点击了确认操作， 新增
                    //把页面赋值为1，然后进行更新操作
                    this.pagenation.page = 1;
                    this.getContactList();
                }
                this.serviceShow = false;
                this.serviceEditShow = false;
            },
            getContactList(type){
                if(type=='search')
                {
                    //是查询
                    this.pagenation.page=1;
                }
                //获取到设备列表
                this.tableLoading = true; //加载中
                var params = {
                    page:this.pagenation.page,
                    pagesize:this.pagenation.pagesize,
                    ...this.searchForm
                }
                capis.getContactList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                        //对总数进行赋值
                        this.pagenation.total=data.res.total;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            sizeChange(pagesize){
                //更改每页的显示条数
                this.pagenation.pagesize = pagesize;
                this.devicesList();
            },
            curreantChange(page)
            {
                this.pagenation.page = page;
                this.devicesList();
            },
            changeStatus(data)
            {
                 this.formLoading=true;
                    //请求接口
                    var params = {
                    act:'changeStatus',
                    id:data.row.id,
                    status:data.row.status
                    }
                capis.contactSave(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        this.$notify({
                            title: '提示',
                            message: '操作成功',
                            type: 'success'
                        });
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            deleteCustom(scope)
            {
                // 删除
                 this.$confirm('确定删除【'+scope.row.name+'】?', '警告', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                        var params = {
                           id:scope.row.id
                        }
                        capis.deleteCustom(params,{})
                        .then(data=>{
                            this.tableLoading = false;
                            if(data.status == 'success'){
                                //去除删掉的数据
                                this.list.splice(scope.$index,1);
                                this.$notify({
                                    type: 'success',
                                    message: '删除成功!'
                                });
                            }else{
                                this.$notify({
                                    title: '提示',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                        .catch(err=>{
                            this.tableLoading = false;
                            this.$notify({
                                title: '提示',
                                message: '操作失败',
                                type: 'warning'
                            });
                        })
                                
                    }).catch(() => {
                        
                });
            },
            editInfo(data)
            {
                this.editId = data.row.id;
                this.serviceEditShow = true ;
            }
        },
        created(){
            //获取到列表
            this.getContactList();
            this.$nextTick(()=>{
                this.getHeight();
                window.onresize = () => {
                    this.getHeight();
                };
            })
        },
        components:{
            CustomAdd
            ,CustomEdit
        },
        computed:{
            
        }
    }
</script>

<style scoped lang="scss">
    .warning{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        height: 100%;
        .message-top{
            .message-title{
                font-size: 24px;
                font-weight: 900;
            }
            .message-type{
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            }
        }
        .message-pagenation{
            text-align: center;
            padding: 10px 0;
        }
    }
</style>
<style>
.service_dialog{
       border-radius: 15px;
       box-shadow: 0 0 30px 0 rgba(94, 94, 198, 0.7);
       box-sizing: border-box;
   }
</style>


