<template>
   <div class="main">
       <!-- 单个盒子-->
       <DevicesView></DevicesView>
       <DevicesWarning></DevicesWarning>
       <DevicesInfo></DevicesInfo>
      <WarningInfo></WarningInfo>
   </div>
</template>
<script>
    import DevicesView from './item/devices_view.vue'
    import DevicesWarning from './item/devices_warning.vue'
    import DevicesInfo from './item/devices_info.vue'
    import WarningInfo from './item/warning_info.vue'
    export default{
        data(){
            return{
                list:[]
            }
        },
        components:{
            DevicesView
            ,DevicesWarning
            ,DevicesInfo
            ,WarningInfo
        }
    }
</script>
<style scoped lang="scss">
    .main{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 20px;
    }
</style>


