<template>
  <div id="app">
    <router-view v-if="isRouterAlive"/>
    <!-- <div class="J-Footer">
        <div class="copyright"> © JINGUC.COM, 2020 , IMS</div>
    </div> -->
  </div>
</template>

<script>
export default {
  name: 'App',
  provide(){
    return{
      reload:this.reload
    }
  },
  data(){
    return{
      isRouterAlive:true
    }
  },
  computed:{
    count(){
      return this.$store.state.count;
    }
  },
  methods:{
    countIncrease(){
      this.$store.commit('countIncrease')
    },
    reload(){
      this.isRouterAlive = false;
      this.$nextTick(function () {
        this.isRouterAlive = true;
      });
    }
  }
}
</script>

<style>
body,p{
  padding: 0;
  margin: 0;
}
