<template>
    <div class="devices">
        <div class="message-top" ref="message-top">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" @keydown.enter.native="getSettingList('search')">
                <el-form-item label="关键字">
                    <el-input v-model="searchForm.keyword" placeholder="请输入" clearable></el-input>
                </el-form-item>
                 <el-form-item label="状态">
                <el-select v-model="searchForm.status" placeholder="请选择" clearable>
                    <el-option label="启用" value="1"></el-option>
                    <el-option label="未启用" value="2"></el-option>
                </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" icon="el-icon-search" @click="getSettingList('search')" :loading="tableLoading">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="message-table">
            <el-table
                ref="record-table"
                :data="list"
                style="width: 100%"
                stripe
                :height = "tableHeight"
                highlight-current-row
                v-loading="tableLoading"
            >
                <el-table-column
                    type="index"
                    width="50">
                </el-table-column>
                <el-table-column
                label="参数名"
                 prop="fd_name"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="类型"
                sortable
                width="80"
                >
                    <template slot-scope="scope">
                       <span v-if="scope.row.type == '1'">前端</span>
                       <span v-if="scope.row.type == '2'">后端</span>
                    </template>
                </el-table-column>
                <el-table-column
                label="参数值"
                >
                    <template slot-scope="scope">
                       <el-input v-model="scope.row.value" placeholder=""></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                label="状态"
                width="50"
                >
                    <template slot-scope="scope">
                        <el-switch
                            v-model="scope.row.status"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            :active-value='1'
                            :inactive-value='2'
                            @change="changeStatus(scope)"
                            >
                        </el-switch>
                    </template>
                </el-table-column>
                 <el-table-column
                    label="说明"
                    >
                    <template slot-scope="scope">
                       <el-input v-model="scope.row.note" placeholder=""></el-input>
                    </template>
                </el-table-column>
                 <el-table-column
                label="更新时间"
                 prop="updated_at"
                sortable
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                label="操作"
                fixed="right"
                width="100"
                >
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="parsmsSave(scope);">保存</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
            <el-pagination
                :page-sizes="[10, 20, 50, 100]"
                :page-size="pagenation.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagenation.total"
                :current-page="pagenation.page"
                @size-change='sizeChange'
                @current-change='curreantChange'
                >
            </el-pagination>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                list:[],//表格数据
                tableHeight:600, // 表格高度
                pagenation:{
                    page:1,
                    pagesize:10,
                    total:0
                },
                tableLoading:false, //表格的加载中
                searchForm:{
                    keyword:'',
                    status:''
                }
            }
        },
        methods:{
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-50;
            },
            getSettingList(type){
                //获取到设备列表
                if(type == 'search')
                {
                    this.pagenation.page = 1;
                }
                this.tableLoading = true; //加载中
                var params = {
                    page:this.pagenation.page,
                    pagesize:this.pagenation.pagesize,
                    ...this.searchForm
                }
                capis.getSettingList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                        //对总数进行赋值
                        this.pagenation.total=data.res.total;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            changeStatus(data)
            {
                this.formLoading=true;
                //请求接口
                    var params = {
                    act:'changeStatus',
                    id:data.row.id,
                    status:data.row.status,
                    }
                capis.paramsSave(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        this.$notify({
                            title: '提示',
                            message: '操作成功',
                            type: 'success'
                        });
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            parsmsSave(data)
            {
                    this.formLoading=true;
                    //请求接口
                     var params = {
                        id:data.row.id,
                        status:data.row.status,
                        value:data.row.value,
                        note:data.row.note
                     }
                    capis.paramsSave(params,{})
                    .then(data=>{
                        if(data.status == 'success'){
                            this.$notify({
                                title: '提示',
                                message: '操作成功',
                                type: 'success'
                            });
                        }else{
                            this.$notify({
                                title: '提示',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    
                    })
                    .catch(err=>{
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                        });
                    })
            },
            sizeChange(pagesize){
                //更改每页的显示条数
                this.pagenation.pagesize = pagesize;
                this.getSettingList();
            },
            curreantChange(page)
            {
                this.pagenation.page = page;
                this.getSettingList();
            }
        },
        created(){
            //获取到服务器列表
            this.getSettingList();
            this.$nextTick(()=>{
                this.getHeight();
                window.onresize = () => {
                    this.getHeight();
                };
            })
        }
    }
</script>

<style scoped lang="scss">
    .devices{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        height: 100%;
        .message-top{
            .message-title{
                font-size: 24px;
                font-weight: 900;
            }
            .message-type{
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            }
        }
        .message-pagenation{
            text-align: center;
            padding: 10px 0;
        }
    }
</style>
<style>
.service_dialog{
       border-radius: 15px;
       box-shadow: 0 0 30px 0 rgba(94, 94, 198, 0.7);
       box-sizing: border-box;
   }
</style>



