<!--
 * @Descripttion: cpu折线图
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-24 14:33:02
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-24 17:47:49
-->
<template>
    <div class="serviceinfo-content">
        <div class="serviceinfo-title">
            <div class="left">
                <p class="serviceinfo-border"></p>
                <p class="serviceinfo-tit">CPU使用率</p>
            </div>
            <div class="right">
                <el-date-picker
                    v-model="time"
                    type="datetimerange"
                    range-separator="~"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    align="right"
                    size="mini"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    @change="changeTime"
                    >
                </el-date-picker>
            </div>
        </div>
        <div class="service-line">
             <div class="line-info">
                <div class="line">
                    <!-- 折线图 -->
                    <div class="" id="cpu" :style="{ height: '360px','margin-bottom':'20px'}"></div>
                </div>
            </div>
        </div>
        <el-divider></el-divider>
    </div>
</template>
<script>
    export default{
        props:{
            hostId:{
                default:'',
            }
        },
        data(){
            return{
                xTitle:null,
                xData:null,
                echarts:null,
                time:'',
                option:null,
                startTime:'',
                endTime:''
            }
        },
        methods:{
           initEchats(){
                this.option = {
                    xAxis: {
                        type: 'category',
                        data: this.xTitle,
                        axisPointer: {
                            lineStyle: {
                                color: '#5e5ec6', //提示线颜色
                                width: 2
                            },
                        },
                    },
                    yAxis: {
                        type: 'value',
                        min:0,
                        axisLabel: {
                            formatter: '{value}%'
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        formatter:(params)=>{
                            return `${params[0].name}<br/>${params[0].marker}${params[0].seriesName}: ${params[0].data}%`
                        }
                    },
                    toolbox: {
                        feature: {
                            saveAsImage: {},
                            restore:{}
                        }
                    },
                    dataZoom: [
                        {
                            type: 'inside',
                            // start:0,
                            // end:30
                        },
                    ],
                    grid:{
                        x:50, //左侧未知
                        x2:0 //右侧
                    },

                    series: [
                        {
                            name:'CPU使用率',
                            data: this.xData,
                            type: 'line',
                            smooth: true,
                            itemStyle:{
                                color:'#5e5ec6' //线的颜色
                            },
                            areaStyle:{
                                normal:{
                                    color:new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                        {
                                            offset: 0,
                                            color: 'rgba(93,115,199,0.5)' //渐变上
                                        },
                                        {
                                            offset: 1,
                                            color: 'rgba(93,115,199,0)' //渐变下
                                        }
                                    ]
                                )}
                                }
                            }
                    ]
                };
                this.echarts = this.$echarts.init(document.getElementById('cpu'));
                this.echarts.setOption(this.option);
            },
            //获取到初始化数据
            getHostInfoEchart(){
                var params = {
                    hostId:this.hostId,
                    type:1,
                    dateType:1,
                    startTime:this.startTime,
                    endTime:this.endTime
                }
                capis.getHostInfoEchart(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.xTitle = data.res.header.reverse();
                        this.xData = data.res.data[0].reverse();
                        this.echarts.setOption({
                                    series: [{
                                        data: this.xData
                                    }],
                                    xAxis:{data:this.xTitle}
                                });
                        
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            changeTime(time){
                this.startTime = time[0];
                this.endTime = time[1];
                this.getHostInfoEchart();
            },
             initWs(){
                this.$LaravelEcho.instance.channel('SysInfo').listen('SysInfoEvent', (e) => {
                    //检测cpu的使用率
                    if(e.type=="CpuInfo")
                    {
                        if(this.hostId==e.host_id)
                        {
                            this.xTitle.push(ctool.getYMDHMS(e.time));
                            this.xData.push(e.data.cpuUse);
                            this.echarts.setOption({
                                series: [{
                                    data: this.xData
                                }],
                                xAxis:{data:this.xTitle}
                            });
                        }
                    }
                })
            }
        },
        components:{
        },
        created(){
            this.initWs();
            this.getHostInfoEchart();
            this.$nextTick(()=>{
                this.initEchats();
            })
        },
    }
</script>

<style scoped lang="scss">
    
    .serviceinfo-content{
        width: 50%;
        .serviceinfo-title{
            display: flex;
            justify-content: space-between;
            font-size: 20px;
            font-weight: 600;
            margin-bottom: 20px;
            .left{
                display: flex;
            }
            .serviceinfo-border{
                width: 10px;
                height: 26px;
                background-color: #5e5ec6;
                border-radius: 5px;
                margin-left: 20px;
            }
            .serviceinfo-tit{
                margin-left: 18px;
            }
        } 
    }
       
</style>


