<!--
 * @Descripttion: 告警与报错
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-26 10:13:34
-->
<template>
    <div class="warning">
        <div class="message-top" ref="message-top">
           <el-form :inline="true" :model="searchForm" class="demo-form-inline" @keydown.enter.native="getNoticeList('search')">
                <el-form-item label="名称">
                    <el-input v-model="searchForm.keyword" placeholder="请输入" clearable></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" icon="el-icon-search" @click="getNoticeList('search')">查询</el-button>
                    <el-button icon="el-icon-circle-plus-outline" type="success" @click="serviceShow = !serviceShow">新增</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="message-table">
            <el-table
            ref="record-table"
            :data="list"
            style="width: 100%"
            stripe
            :height="tableHeight"
            highlight-current-row
            v-loading="tableLoading"
            >
                
                 <el-table-column
                    type="index"
                    width="50">
                </el-table-column>
                <el-table-column
                label="规则标题"
                prop="title"
                show-overflow-tooltip
                >
                    
                </el-table-column>
                <el-table-column
                label="告警类型"
                width="150"
                show-overflow-tooltip
                >
                 <template slot-scope="scope">
                     <p v-for="item in warningType" :key="item.typeId">
                         <span v-if="scope.row.type == item.typeId">{{item.name}}</span>
                     </p>
                </template>
                </el-table-column>
                <el-table-column
                label="告警方式"
                show-overflow-tooltip
                >
                 <template slot-scope="scope">
                    <p v-if="scope.row.sendType == 1">电话</p>
                    <p v-else-if="scope.row.sendType == 2">短信 </p>
                    <p v-else-if="scope.row.sendType == 3">邮件 </p>
                    <p v-else-if="scope.row.sendType == 4">电话+短信</p>
                    <p v-else-if="scope.row.sendType == 5">电话+邮件</p>
                    <p v-else-if="scope.row.sendType == 6">短信+邮件</p>
                    <p v-else-if="scope.row.sendType == 7">电话+短信+邮件</p>
                </template>
                </el-table-column>
                
                <el-table-column
                prop="Contact"
                label="联系人"
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                prop="content"
                label="告警内容"
                show-overflow-tooltip
                >
                </el-table-column>
                <el-table-column
                prop="updated_at"
                label="修改时间"
                show-overflow-tooltip
                >
                </el-table-column>
                 <el-table-column
                label="状态"
                sortable
                >
                <template slot-scope="scope">
                    <el-switch
                        v-model="scope.row.status"
                        active-color="#13ce66"
                        inactive-color="#ff4949"
                        :active-value='1'
                        :inactive-value='2'
                        @change="changeStatus(scope)"
                        >
                    </el-switch>
                </template>
                </el-table-column>
                <el-table-column
                prop="updated_at"
                label="操作"
                width="200"
                >
                 <template slot-scope="scope">
                    <el-button type="primary" icon="el-icon-edit" size="mini" @click="editInfo(scope);">编辑</el-button>
                    <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteWarning(scope);">删除</el-button>
                </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="message-pagenation" ref="message-pagenation">
           <el-pagination
                :page-sizes="[10, 20, 50, 100]"
                :page-size="pagenation.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagenation.total"
                :current-page="pagenation.page"
                @size-change='sizeChange'
                @current-change='curreantChange'
                >
            </el-pagination>
        </div>
       <el-dialog width="50%" :visible.sync="serviceShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog">
            <WarningAdd  v-on:freshData="freshData" v-if="serviceShow"></WarningAdd>
        </el-dialog>
        <el-dialog width="50%" :visible.sync="serviceEditShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog">
            <WarningEdit  v-on:freshData="freshData" v-if="serviceEditShow" :editId="editId"></WarningEdit>
        </el-dialog>
    </div>
</template>
<script>
    import WarningAdd from './add/add.vue'
    import WarningEdit from './add/edit.vue'
    export default{
        data(){
            return{
                searchForm:{
                    keyword:''
                },
                list:[],//表格数据
                tableHeight:600, // 表格高度
                serviceShow:false,
                serviceEditShow:false,
                tableLoading:false, //表格的加载中
                pagenation:{
                    page:1,
                    pagesize:10,
                    total:0
                },
                //告警类型
                warningType:null
            }
        },
        methods:{
            getHeight(){
                this.tableHeight = window.innerHeight-this.$refs['record-table'].$el.offsetTop-40
            },
            getNoticeList(type){
                //获取到列表
                 if(type=='search')
                {
                    //是查询
                    this.pagenation.page=1;
                }
                //获取到设备列表
                this.tableLoading = true; //加载中
                var params = {
                    page:this.pagenation.page,
                    pagesize:this.pagenation.pagesize,
                    ...this.searchForm
                }
                capis.getNoticeList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                        //对总数进行赋值
                        this.pagenation.total=data.res.total;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
             sizeChange(pagesize){
                //更改每页的显示条数
                this.pagenation.pagesize = pagesize;
                this.getNoticeList();
            },
            curreantChange(page)
            {
                this.pagenation.page = page;
                this.getNoticeList();
            },
            deleteWarning(scope)
            {
                // 删除
                 this.$confirm('确定删除【'+scope.row.title+'】?', '警告', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                    }).then(() => {
                        var params = {
                           id:scope.row.id
                        }
                        capis.deleteWarning(params,{})
                        .then(data=>{
                            this.tableLoading = false;
                            if(data.status == 'success'){
                                //去除删掉的数据
                                this.list.splice(scope.$index,1);
                                this.$notify({
                                    type: 'success',
                                    message: '删除成功!'
                                });
                            }else{
                                this.$notify({
                                    title: '提示',
                                    message: data.des,
                                    type: 'warning'
                                });
                            }
                        
                        })
                        .catch(err=>{
                            this.tableLoading = false;
                            this.$notify({
                                title: '提示',
                                message: '操作失败',
                                type: 'warning'
                            });
                        })
                                
                    }).catch(() => {
                        
                });
            },
             changeStatus(data)
            {
                 this.formLoading=true;
                    //请求接口
                     var params = {
                        act:'changeStatus',
                        id:data.row.id,
                        status:data.row.status
                     }
                    capis.warningStatusEdit(params,{})
                    .then(data=>{
                        if(data.status == 'success'){
                            this.$notify({
                                title: '提示',
                                message: '操作成功',
                                type: 'success'
                            });
                        }else{
                            this.$notify({
                                title: '提示',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    
                    })
                    .catch(err=>{
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                        });
                    })
            },
            freshData(data)
            {
                // 子组件给父组件传的至
                if(data && data.type == 'cancel')
                {
                    // 子组件取消操作,隐藏弹出层
                    
                }
                else if(data && data.type == 'update')
                {
                    //子组件点击了确认操作， 编辑的更新操作
                    this.getNoticeList();
                }
                else if(data && data.type == 'add')
                {
                    //子组件点击了确认操作， 新增
                    //把页面赋值为1，然后进行更新操作
                    this.pagenation.page = 1;
                    this.getNoticeList();
                }
                this.serviceShow = false;
                this.serviceEditShow = false;
            },
            editInfo(data)
            {
                this.editId = data.row.id;
                this.serviceEditShow = true ;
            },
            getWarnType(){
                this.warningType = ctool.getWarningType();
            }
        },
        created(){
            this.$nextTick(()=>{
                this.getHeight();
                window.onresize = () => {
                    this.getHeight();
                };
            })
            //获取到列表
            this.getNoticeList();
            //获取到告警类型
            this.getWarnType();
        },
        components:{
            WarningAdd
            ,WarningEdit
        },
    }
</script>

<style scoped lang="scss">
    .warning{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        height: 100%;
        .message-top{
            .message-title{
                font-size: 24px;
                font-weight: 900;
            }
            .message-type{
                box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            }
        }
        .message-pagenation{
            text-align: center;
            padding: 10px 0;
        }
    }
</style>
<style>
.service_dialog{
       border-radius: 15px;
       box-shadow: 0 0 30px 0 rgba(94, 94, 198, 0.7);
       box-sizing: border-box;
   }
</style>


