-->
<template>
   <div class="gl">
       <div class="gl_box">
           <div class="gl_main">
                <div class="gl_bg">
                    <div class="bg_in"></div>
                    <div class="bg_out"></div>
                    <p class="title">
                        <img src="../../../assets/welcom/title_zkt.png" alt="主控台">
                    </p>
                    <div class="item">
                        <div class="item_all">
                            <div class="item_bg" style="top:-100px;left:-200px" @click="serviceShow = !serviceShow">
                                <i class="el-icon-success item_pso_icon success"></i>
                                <span>主机组</span>
                            </div>
                             <div class="item_bg" style="top:50px;left:-300px">
                                <i class="el-icon-error item_pso_icon error"></i>
                                <span>设备状态</span>
                            </div>
                            <div class="item_bg" style="top:250px;left:-280px">
                                <i class="el-icon-warning item_pso_icon warning"></i>
                                <span>主机组</span>
                            </div>
                        </div>
                         <div class="item_all">
                            <div class="item_bg" style="top:-100px;right:-200px">
                                <i class="el-icon-success item_pso_icon success"></i>
                                <span>主机组</span>
                            </div>
                             <div class="item_bg" style="top:50px;right:-300px">
                                <i class="el-icon-error item_pso_icon error"></i>
                                <span>设备状态</span>
                            </div>
                            <div class="item_bg" style="top:250px;right:-280px">
                                <i class="el-icon-warning item_pso_icon warning"></i>
                                <span>主机组</span>
                            </div>
                        </div>
                    </div>
                </div>
           </div>
           
       </div>
       <el-dialog width="1383px" :visible.sync="serviceShow" :lock-scroll="true" :center="true" :show-close="false" custom-class="service_dialog">
            <ServiceInfo></ServiceInfo>
        </el-dialog>
   </div>
</template>
<script>
    import ServiceInfo from './service_info.vue'
    export default{
        data(){
            return{
                serviceShow:false
            }
        },
        components:{
            ServiceInfo
        }
    }
</script>
<style scoped lang="scss">
    @keyframes rotate {
        from 
        {
            background-position-x: 0px;
        }
        to 
        {
            background-position-x: -800px;
        }
    }
    @keyframes rotate360 {
        from 
        {
           transform: rotate(0deg);
        }
        to 
        {
            transform: rotate(359deg);
        }
    }
    @keyframes rotate0 {
        from 
        {
           transform: rotate(359deg);
        }
        to 
        {
            transform: rotate(0deg);
        }
    }
   .gl{
       height: 100%;
        .gl_box{
            height: 100%;
        }
        .gl_main{
            display: flex;
            height: 100%;
            justify-content: center;
            align-items: center;
            
             .gl_bg{
                position: relative;
                background: url(../../../assets/welcom/GL_bg3.jpg);
                background-size: 800px;
                width: 400px;
                height: 400px;
                border-radius: 50%;
                animation-name: rotate;
                animation-duration: 10s;
                animation-iteration-count: infinite;
                animation-timing-function: linear;
                // border: 1px solid #f00;
                .bg_in{
                    position: absolute;
                    top: -100px;
                    left: -100px;
                    width: 600px;
                    height: 600px;
                    background: url(../../../assets/welcom/bg_in.png) no-repeat;
                    animation: rotate360 15s infinite linear;
                }
                .bg_out{
                    position: absolute;
                    top: -100px;
                    left: -100px;
                    width: 600px;
                    height: 600px;
                    background: url(../../../assets/welcom/bg_out.png) no-repeat;
                    animation: rotate0 15s infinite linear;
                }
                .title{
                    font-size: 40px;
                    text-align: center;
                    margin-top: 180px;
                    
                }
                .item{
                    .item_all{
                        .item_bg{
                            box-sizing: border-box;
                            position: absolute;
                            // top: -100px;
                            // left: -400px;
                            cursor: pointer;
                            background: url(../../../assets/welcom/item_bg.jpg);
                            background-size: 82px;
                            width: 82px;
                            height: 82px;
                            font-size: 18px;
                            color: #5e5ec6;
                            padding: 19px;
                            text-align: center;
                            .success{
                                color: #95d871;
                            }
                            .warning{
                                color:#fed361
                            }
                            .error{
                                color: #ff3838;
                            }
                            .question{
                                color: #999999;
                            }
                            .item_pso_icon{
                                position: absolute;
                                top: -10px;
                                left: -10px;
                                font-size: 24px;
                            }
                        }
                    }
                    

                }
            }
        }
      
   }
   .el-dialog--center .el-dialog__body{
       padding: 0;
   }
   
</style>
<style>
.service_dialog{
       border-radius: 15px;
       box-shadow: 0 0 30px 0 rgba(94, 94, 198, 0.7);
       box-sizing: border-box;
   }
</style>


