import Vue from 'vue'
import axios from 'axios'
import QS from 'qs'
import router from '@/router'  //引入router
import sysParams from '../utils/sys_params'
// import capis from '../utils/commTool'
const config = require('@/utils/pyconfig')
//url前缀
axios.defaults.baseURL = config.ROOT;
//默认超时时间
axios.defaults.timeout = 200000;

// 请求过滤,显示加载中
axios.interceptors.request.use(
  config => {
    let otyp1 = Object.prototype.toString.call(config.data);
    let Authorization = {"Authorization":localStorage.getItem('token_type')+' '+localStorage.getItem('access_token')};
    config.headers={...config.headers,...Authorization};
    if(otyp1 === '[object Object]')
    {
      config.data = QS.stringify(config.data);
    }
    let otyp = Object.prototype.toString.call(config.data);
    //只有当data是from类型，并且有noloading字段时候不添加加载等待效果
    if(otyp === '[object FormData]')
    {
      Object.assign(config.headers,{'Content-Type':"multipart/form-data"});
    }
    else
    {
      // Object.assign(config.headers,{'Content-Type':"application/x-www-form-urlencoded/charset=utf-8"});
      if (!(((otyp === '[object String]')) && (config.data.indexOf('noloading') > 0))) 
      {
        if(config.data.indexOf('noloading')<0)
        {
        }
      }
    }
    if(config.url !== '/api/auth/login' || config.url !== '/api/auth/logout')
    {
      if(localStorage.getItem('access_token')){
        // 获取到access_token 
        let access_token = localStorage.getItem('access_token'); 
        // 获取到expires_in 
        let expires_in = localStorage.getItem('expires_in'); 
        // 获取到token_type 
        let token_type = localStorage.getItem('token_type'); 
        // 获取到token_time  登录时间戳
        let token_time = localStorage.getItem('token_time'); 
        if(expires_in && token_type && token_time){
          //判断所属数据存在
          // 获取到当前时间戳
          let timeStamp = new Date().getTime();
          // 判断access_token的时间是否超期
          if((timeStamp - token_time)<=(expires_in*1000))
          {
            window.localStorage.setItem('token_time',new Date().getTime());
            // 验证通过
          }
          else
          {
            ctool.gotLogin({
              title: '登录超时',
              message: '登录超时，请重新登录',
              type: 'fail'
            });
          }
        }
        else
        {
          // 判断所需数据不存在，跳转登录
            ctool.gotLogin({
              title: '未登录',
              message: '当前用户未登录，请登陆后使用',
              type: 'fail'
            });
        }
      }
      else
      {
        // 判断所需数据不存在，跳转登录
          ctool.gotLogin({
            title: '未登录',
            message: '当前用户未登录，请登陆后使用',
            type: 'fail'
          });
      }
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

//收到数据后的过滤
axios.interceptors.response.use(
    response => {
        if(response.headers.authorization)
        {
            var access_token = response.headers.authorization;
            window.localStorage.setItem('access_token',access_token.replace("jinGuEoms/",""));
            window.localStorage.setItem('token_time',new Date().getTime());
        }
        if (response.status === 200) {
            return Promise.resolve(response);
        } else if(response.status === 419) {
            ctool.gotLogin({
              title: '未登录',
              message: '当前用户未登录，请登陆后使用',
              type: 'fail'
            });
            return Promise.reject(response);
        }
        else {
            return Promise.reject(response);
        }
    },
    error => {
        if(error.response.status === 401) {
            ctool.gotLogin({
              title: '未登录',
              message: '当前用户未登录，请登陆后使用',
              type: 'fail'
            });
        }
        return Promise.resolve(error.response);
    }
);


export function get(url, params) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    }).then(res => {
      
      resolve(res);
    }).catch(err => {
      reject(err)
    })
  });
}

/*
 * 入参会被序列化，后台使用getParameter一个一个获取
 */
export function post(url, params,headers) {
  return new Promise((resolve, reject) => {
    var header = {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Accept' : 'application/json'
    };
    if(!headers){headers = {}};
    headers = {...header, ...headers};
    axios({
      method: "POST",
      url: url,
      data:params,
      headers:headers
    })
      .then(res => {
        if(res.data.status == 'success')
        {
          //把数据打印出来
          if(url !== '/api/auth/login')
          {
            if(res.data.res && res.data.res.length > 0){
              res.data.res = JSON.parse(ctool.getAs(res.data.res));   
              if (process.env.NODE_ENV === 'development') {//只在dev环境下打印
                console.log('debug打印输出：', url, res.data.res)            
              } 
            }
          }
        }
        resolve(res.data);
      })
      .catch(err => {
        reject(err)
      })
  });
}
export function postBlob(url, params) {
  return new Promise((resolve, reject) => {
    axios({
      method: "POST",
      url: url,
      data:params,
      headers: {
      },
      responseType: 'blob'
    }).then(res => {
      resolve(res.data);
    })
    .catch(err => {
      reject(err.data)
    })
  });
}



