<template>
    <div class="services-list">
        <div class="service-item">
            <div class="service-title">
                <p class="service-border"></p>
                <p class="service-tit">服务器</p>
            </div>
            <div class="service-content" v-if="list">
                <div class="service-info" v-for="(item,index) in list" :key="index">
                    <div class="service-content-top" @click="routeToDetail(item.id)">
                        <div class="service-icon">
                            <i class="el-icon-office-building"></i>
                        </div>
                        <div class="service-name">
                            <p class="service-name-title">{{item.name}}</p>
                            <div class="service-name-info">
                                <p>{{item.host}}</p>
                                <div class="service-status"> 
                                    <p class="status-dot" :class="item.running?'success':'stop'"></p>
                                    <p v-if="item.running">在线</p> <p v-if="!item.running">离线</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="service-content-bottom">
                        <div class="service-content-bottom-process">
                            <!-- 百分比数据 -->
                            <div class="process">
                                <p class="process-title" style="color:#5e5ec6;">CPU</p>
                                <el-progress type="circle" :percentage="parseFloat(item.cpuUse)" :width="60" :stroke-width="5" color="#5e5ec6"></el-progress>
                            </div>
                            <div class="process">
                                <p class="process-title" style="color:#cf49ca;">内存</p>
                                <el-progress type="circle" :percentage="parseFloat(item.memUsedPercent)" :width="60" :stroke-width="5" color="#cf49ca"></el-progress>
                            </div>
                            <div class="process">
                                <p class="process-title" style="color:#ff5722;">负载</p>
                                <el-tooltip placement="top">
                                    <div slot="content">1分钟负载{{item.cpuLoad1}}<br/>5分钟负载{{item.cpuLoad5}}<br/>15分钟负载{{item.cpuLoad15}}</div>
                                    <p class="process-title" style="color:#ff5722;">负载</p>
                                    <div>
                                        <p class="fuzai">{{item.cpuLoad1}}</p>
                                        <p class="fuzai">{{item.cpuLoad5}}</p>
                                        <p class="fuzai">{{item.cpuLoad15}}</p>
                                    </div>
                                </el-tooltip>
                            </div>
                            <div class="process">
                                <p class="process-title" style="color:#95d871">网络</p>
                                <el-tooltip placement="top">
                                    <div slot="content">上行{{item.netSpeed.out.size}}{{item.netSpeed.out.format}}<br/>下行{{item.netSpeed.in.size}}{{item.netSpeed.in.format}}</div>
                                    <div>
                                        <p class="web">{{item.netSpeed.out.size}} {{item.netSpeed.out.format}}/s<i class="el-icon-caret-top"></i></p>
                                        <p class="web">{{item.netSpeed.in.size}} {{item.netSpeed.in.format}}/s<i class="el-icon-caret-bottom"></i></p>
                                    </div>
                                </el-tooltip>
                            </div>
                        </div>
                        <div class="service-content-bottom-services" v-if="item.role">
                            <span v-for="(it,ind) in item.role" :key="ind">
                                <el-button type="warning" plain round size="mini" v-for="(i,k) in it" :key="k">{{i}}</el-button>
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default{
        data(){
            return{
               list:[]
            }
        },
        methods:{
           getEnableServices(){
               var params = {}
                capis.getEnableServices(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
           },
           routeToDetail(id)
           {
               this.$router.push({path:'/index/services/servicesinfo',query:{hostId:id}})
           },
           initWs(){
                //检测主机状态
                this.$LaravelEcho.instance.channel('HostStatus').listen('HostEvent', (e) => {
                    if(e.type=="status")
                    {
                        if(this.list.length>0)
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].running = e.data.running;
                                }
                            });
                        }
                    }
                    else if(e.type=="HostAdd" || e.type=="HostDel" || e.type=="HostChange")
                    {
                        this.getEnableServices();
                    }
                })
                this.$LaravelEcho.instance.channel('SysInfo').listen('SysInfoEvent', (e) => {
                    if(this.list.length>0)
                    {
                        //检测cpu的使用率
                        if(e.type=="CpuInfo")
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].cpuUse = e.data.cpuUse;
                                    this.list[index].cpuLoad1 = e.data.cpuLoad1;
                                    this.list[index].cpuLoad5 = e.data.cpuLoad5;
                                    this.list[index].cpuLoad15 = e.data.cpuLoad15;
                                }
                            });
                        }
                        //内存的使用率
                        else if(e.type =='MemoryInfo')
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].memUsedPercent = e.data.memUsedPercent;
                                }
                            });
                        }
                        //网络
                        else if(e.type =='ifInfo')
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].netSpeed.in.size = e.data.escape.in.size; //下行量
                                    this.list[index].netSpeed.in.format = e.data.escape.in.format; //下行单位
                                    this.list[index].netSpeed.out.size = e.data.escape.out.size; //上行量
                                    this.list[index].netSpeed.out.format = e.data.escape.out.format; //上行单位
                                }
                            });
                        }
                    }
                })
            }
        },
        created(){
            this.getEnableServices();
            this.$nextTick(()=>{
                this.initWs();
                window.onresize = () => {
                    this.getHeight();
                };
            })
        },
        beforeDestroy(){
            this.$LaravelEcho.instance.leaveChannel('SysInfo');
            this.$LaravelEcho.instance.leaveChannel('HostStatus');
        }
    }
</script>

<style scoped lang="scss">
    .services-list{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        .service-item{
            .service-title{
                display: flex;
                font-size: 20px;
                font-weight: 600;
                .service-border{
                    width: 10px;
                    height: 26px;
                    background-color: #5e5ec6;
                    border-radius: 5px;
                }
                .service-tit{
                    margin-left: 20px;
                }
            }
            .service-content{
                padding: 20px 0;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                
                .service-info{
                    cursor: pointer;
                    box-sizing: border-box;
                    width: 345px;
                    border: 2px solid #cccccc;
                    border-radius: 10px;
                    padding: 18px 24px;
                    // display: flex;
                    margin: 0 20px 20px 0;
                    .service-content-top{
                        display: flex;
                        flex-direction: row;
                        flex-wrap: wrap;
                        border-bottom: 1px solid #eee;
                        .service-icon{
                            font-size: 44px;
                        }
                        .service-name{
                            margin-left: 28px;
                            flex: 1;
                            .service-name-title{
                                font-size: 20px;
                                font-weight: 500;
                            }
                            .service-name-info{
                                display: flex;
                                flex-direction: row;
                                justify-content: space-between;
                                width: 100%;
                                margin-top: 5px;
                                font-size: 14px;
                                color: #666;
                                .service-status{
                                    display: flex;
                                    align-items: center;
                                    .status-dot{
                                        height: 12px;
                                        width: 12px;
                                        border-radius: 50%;
                                        margin-right: 5px;
                                    }
                                    .success{
                                        background-color: rgb(5, 243, 96);
                                    }
                                    .stop{
                                        background-color: red;
                                    }
                                }
                                
                            }
                        }
                    }
                    .service-content-bottom{
                        .service-content-bottom-process{
                            display: flex;
                            box-sizing: border-box;
                            justify-content: space-between;
                            .process{
                                width: 25%;
                                text-align: center;
                                .process-title{
                                    font-size: 14px;
                                    height: 36px;
                                    line-height: 36px;
                                }
                                .web{
                                    text-align: right;
                                    font-size: 12px;
                                    height: 30px;
                                    line-height: 30px;
                                    // font-weight:550;
                                    i{
                                        font-size: 12px;
                                        color: #95d871;
                                        vertical-align: unset;
                                    }
                                }
                                .fuzai{
                                    font-size: 12px;
                                    height: 20px;
                                    line-height: 20px;
                                    // font-weight: bolder;
                                }
                            }
                        }
                        .service-content-bottom-services{
                           .el-button{
                                margin-top: 10px;
                                margin-left: 10px;
                           }
                           
                        }
                    }
                    
                }
                .service-info:hover{
                    border: 2px solid #5e5ec6;
                }
            }
        }
    }
</style>


