<!--
 * @Descripttion: 设备管理添加
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 23:06:31
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-07-06 17:56:50
-->
<template>
   <div class="warning-add">
       <p class="warning-add-title">基础选项</p>
       <el-form ref="form" :model="form" :rules="rules" label-width="100px" v-loading="formLoading">
            <el-form-item label="设备类型：" prop="type">
                 <el-select v-model="form.type" placeholder="请选择" clearable>
                    <el-option
                        label="centos服务器"
                        :value="1">
                    </el-option>
                </el-select>
            </el-form-item>
             <el-form-item label="告警规则：" prop="NotificationSettingId">
                 <el-select v-model="form.NotificationSettingId" multiple placeholder="请选择" clearable>
                    <el-option
                        v-for="(value,key) in NotificationSettingIdArr"
                        :key="key"
                        :label="value.title"
                        :value="value.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="名　　称：" prop="name">
                <el-input v-model="form.name" clearable></el-input>
            </el-form-item>
            <el-form-item label="地　　址：" prop="host">
                <el-input v-model="form.host" clearable></el-input>
            </el-form-item>
            <el-form-item label="账　　号：" prop="username">
                <el-input v-model="form.username" clearable></el-input>
            </el-form-item>
            
            <el-form-item label="密　　码：" prop="password">
                <el-input v-model="form.password" clearable type='password' show-password></el-input>
            </el-form-item>
             <el-form-item label="服务类型：" prop="role">
                 <el-checkbox-group v-model="form.role" size="mini">
                    <el-checkbox-button v-for="(value,key) in roleList" :key="value" :label="key"></el-checkbox-button>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="状　　态：" prop="status">
                 <el-switch
                    v-model="form.status"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    :active-value='1'
                    :inactive-value='2'
                    >
                </el-switch>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('form')" :loading="formLoading">确定</el-button>
                <el-button @click="cancel">取消</el-button>
            </el-form-item>
        </el-form>
   </div>
</template>
<script>
    export default{
        props:{
            editId:{
                default:''
            }
        },
        data(){
            return{
                form:{
                    role:[], //服务类型
                    username:'',
                    name:'',
                    password:'',
                    host:'',
                    type:'', // 服务器类型
                    status:'',
                    NotificationSettingId:[],

                },
                rules: {
                    // role: [
                    //     { required: true, message: '请选择服务类型', trigger: 'blur' },
                    // ],
                    name: [
                        { required: true, message: '请输入名称', trigger: 'blur' },
                    ],
                    host: [
                        { required: true, message: '请输入地址', trigger: 'blur' },
                    ],
                    type: [
                        { required: true, message: '请选择类型', trigger: 'blur' },
                    ],
                },
                roleList:null, // 服务器角色的数据
                formLoading:false,
                NotificationSettingIdArr:null //告警规则
            }
        },
        components:{
        },
        created(){
            this.getDevicesInfo();
            this.getRoleList();
            this.getNotificationSettingListEnable();
        },
        methods: {
          submitForm(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.formLoading=true;
                    //请求接口
                     var params = {
                        role:this.form.role && this.form.role instanceof Array && this.form.role.length>0?this.form.role.join(','):'', //服务器类型
                        username:this.form.username,
                        name:this.form.name,
                        password:this.form.password,
                        host:this.form.host,
                        id:this.editId,
                        type:this.form.type,
                        NotificationSettingId:this.form.NotificationSettingId && this.form.NotificationSettingId instanceof Array && this.form.NotificationSettingId.length>0?this.form.NotificationSettingId.join(','):'', //服务器类型
                        status:this.form.status
                     }
                    capis.devicesSave(params,{})
                    .then(data=>{
                        this.formLoading = false;
                        if(data.status == 'success'){
                            //对数据进行赋值
                            // 修改父组件的值
                            this.$emit('freshData',{
                                type:'update',
                            })
                        }else{
                            this.$notify({
                                title: '提示',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    
                    })
                    .catch(err=>{
                        this.formLoading = false;
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                        });
                    })
                    
                }
            });
          },
          getRoleList(){
                var params = {};
                capis.getRoleList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.roleList = data.res;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
          },
          getDevicesInfo(){
              var params = {
                  id:this.editId
              };
                capis.getDevicesInfo(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        //对数据进行赋值
                        // this.form.username = data.res.data.user_name;
                        this.form.name = data.res.data.name;
                        // this.form.password = data.res.data.password;
                        this.form.type = data.res.data.type;
                        this.form.host = data.res.data.host;
                        this.form.status = data.res.data.status;
                        console.log(data.res.data.NotificationSettingId.split(','),data.res.data.NotificationSettingId.length)
                        this.form.NotificationSettingId = data.res.data.NotificationSettingId?data.res.data.NotificationSettingId.split(','):''
                        this.form.role = data.res.data.role?data.res.data.role.split(','):[]
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
          },
           getNotificationSettingListEnable(){
              var params = {};
                capis.getNotificationSettingListEnable(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.NotificationSettingIdArr = data.res.data;
                        this.NotificationSettingIdArr.forEach((value,index) => {
                            console.log(value,index)
                            this.NotificationSettingIdArr[index].id = value.id.toString();
                        });
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
          },
          cancel(){
            this.$emit('freshData',{
                type:'cancel',
            })
          }
        },
        
    }
</script>
<style scoped lang="scss">
   .warning-add{
       padding: 0 80px;
       .warning-add-title{
           font-size: 28px;
           font-weight: 600;
           height: 60px;
           line-height: 60px;
       }
       .el-select{
           display: block;
       }
   }
</style>


