<!--
 * @Descripttion: 警告与告警添加
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 23:06:31
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-24 15:03:54
-->
<template>
   <div class="warning-add">
       <p class="warning-add-title">基础选项</p>
       <el-form ref="form" :model="form" :rules="rules" label-width="100px">
            <el-form-item label="规则名称：" prop="title">
                <el-input v-model="form.title" clearable></el-input>
            </el-form-item>
            <el-form-item label="告警类型：" prop="type">
                <el-select v-model="form.type" placeholder="请选择" style="width:100%">
                    <el-option
                    v-for="item in warningType"
                    :key="item.typeId"
                    :label="item.name"
                    :value="item.typeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="通知：" prop="sendType">
                <el-col :span="11">
                    <el-form-item prop="sendType">
                        <el-select v-model="form.sendType" placeholder="通知方式" style="width:100%;">
                            <el-option :value="1" label="电话"> </el-option>
                            <el-option :value="2" label="短信"> </el-option>
                            <el-option :value="3" label="邮件"> </el-option>
                            <el-option :value="4" label="电话+短信"> </el-option>
                            <el-option :value="5" label="电话+邮件"> </el-option>
                            <el-option :value="6" label="短信+邮件"> </el-option>
                            <el-option :value="7" label="电话+短信+邮件"> </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <p style="display:inline-block;width:10px;">
                    </p>
                </el-col>
                <el-col :span="12">
                    <el-form-item prop="ContactId">
                        <el-select v-model="form.ContactId" multiple=""  placeholder="人员" style="width:100%;">
                            <el-option 
                            v-for="item in contact"
                            :key="item.id"
                            :value="item.id" 
                            :label="item.name"
                            > </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <el-form-item label="周期：">
                <el-col :span="8">
                    <el-form-item prop="cycle">
                        <el-input v-model="form.cycle" clearable placeholder="统计周期" type="number"><template slot="append">分钟</template></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <p style="display:inline-block;width:10px;">
                    </p>
                </el-col>
                <el-col :span="7">
                    <el-form-item prop="continueCycle">
                        <el-input v-model="form.continueCycle" clearable placeholder="持续周期" type="number"><template slot="append">分钟</template></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <p style="display:inline-block;width:10px;">
                    </p>
                </el-col>
                <el-col :span="7">
                    <el-form-item prop="silenceCycle">
                        <el-input v-model="form.silenceCycle" clearable placeholder="沉默周期" type="number"><template slot="append">分钟</template></el-input>
                    </el-form-item>
                </el-col>
            </el-form-item>
             <el-form-item label="阈值：" prop="countType">
                <el-col :span="8">
                    <el-form-item prop="countType">
                        <el-select v-model="form.countType" placeholder="统计类型">
                            <el-option
                            label="最大值"
                            :value="1">
                            </el-option>
                            <el-option
                            label="最小值"
                            :value="2">
                            </el-option>
                            <el-option
                            label="平均值"
                            :value="3">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <p style="display:inline-block;width:10px;">
                    </p>
                </el-col>
                <el-col :span="7">
                    <el-form-item prop="operator">
                        <el-select v-model="form.operator" placeholder="运算符">
                            <el-option
                            label="大于"
                            :value="1">
                            </el-option>
                            <el-option
                            label="大于等于"
                            :value="2">
                            </el-option>
                            <el-option
                            label="等于"
                            :value="3">
                            </el-option>
                            <el-option
                            label="小于等于"
                            :value="4">
                            </el-option>
                            <el-option
                            label="小于等于"
                            :value="5">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                    <p style="display:inline-block;width:10px;">
                    </p>
                </el-col>
                <el-col :span="7">
                    <el-form-item prop="value">
                        <el-input v-model="form.value" clearable placeholder="阈值" type="number"></el-input>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <el-form-item label="有限时段：" prop="start">
                <el-col :span="11">
                    <el-form-item prop="start">
                        <el-time-picker
                            v-model="form.start"
                            placeholder="任意时间点"
                            style="width:100%"
                            value-format="HH:mm:ss"
                            >
                        </el-time-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="2">
                    <p style="text-align:center;">
                        至
                    </p>
                </el-col>
                <el-col :span="11">
                    <el-form-item prop="end">
                        <el-time-picker
                            v-model="form.end"
                            placeholder="任意时间点"
                            style="width:100%"
                             :picker-options="{
                                minTime: form.start
                            }"
                            value-format="HH:mm:ss"
                            >
                        </el-time-picker>
                    </el-form-item>
                </el-col>
            </el-form-item>
             <el-form-item label="告警内容：" prop="content">
                <el-input v-model="form.content" clearable></el-input>
            </el-form-item>
             <el-form-item label="告警状态：" prop="status">
                 <el-switch
                    v-model="form.status"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    :active-value='1'
                    :inactive-value='2'
                    >
                </el-switch>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('form')">确定</el-button>
                <el-button @click="cancel">取消</el-button>
            </el-form-item>
        </el-form>
   </div>
</template>
<script>
    export default{
         props:{
            editId:{
                default:''
            }
        },
        data(){
            return{
                form:{
                    title:'', //规则名称
                    type:'', //告警类型
                    sendType:'', //告警方式
                    cycle:'', //统计周期
                    continueCycle:'',//持续周期
                    countType:'',//统计类型
                    operator:'',//运算符
                    silenceCycle:'',//沉默周期
                    value:'',//阈值
                    start:'',//有效开始时间
                    end:'',//有效开始时间
                    ContactId:'',//通讯录id
                    content:'',//告警内容
                    status:'1', //状态
                    warningType:null
                    
                },
                contact:null, //启用的用户
                 rules: {
                    title: [
                        { required: true, message: '请输入名称', trigger: 'blur' },
                    ],
                    type: [
                        { required: true, message: '请选择类型', trigger: 'blur' },
                    ],
                    sendType: [
                        { required: true, message: '请选择通知方式', trigger: 'blur' },
                    ],
                    cycle: [
                        { required: true, message: '请输入统计周期', trigger: 'blur' },
                    ],
                    continueCycle: [
                        { required: true, message: '请输入持续周期', trigger: 'blur' },
                    ],
                    countType: [
                        { required: true, message: '请选择统计类型', trigger: 'blur' },
                    ],
                    operator: [
                        { required: true, message: '请选择运算符', trigger: 'blur' },
                    ],
                    silenceCycle: [
                        { required: true, message: '请输入沉默周期', trigger: 'blur' },
                    ],
                    value: [
                        { required: true, message: '请输入阈值', trigger: 'blur' },
                    ],
                    start: [
                        { required: true, message: '请选择开始时间', trigger: 'blur' },
                    ],
                    end: [
                        { required: true, message: '请选择结束时间', trigger: 'blur' },
                    ],
                    ContactId: [
                        { required: true, message: '请选择联系人', trigger: 'blur' },
                    ],
                    content: [
                        { required: true, message: '请输入告警内容', trigger: 'blur' },
                    ],
                    status: [
                        { required: true, message: '请选择状态', trigger: 'blur' },
                    ],
                }
            }
        },
        components:{
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                if (valid) {
                     var params = {...this.form};
                     params.id = this.editId;
                     params.ContactId = params.ContactId.join(',');
                    capis.getNoticeSave(params,{})
                    .then(data=>{
                        this.tableLoading = false;
                        if(data.status == 'success'){
                            //对数据进行赋值
                             this.$emit('freshData',{
                                type:'update',
                            })
                        }else{
                            this.$notify({
                                title: '提示',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    
                    })
                    .catch(err=>{
                        this.tableLoading = false;
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                        });
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
            },
            getContactListEnable(){
                //获取到人员
                var params = {};
                capis.getContactListEnable(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.contact = data.res.data;
                        for(var i=0; i<this.contact.length; i++)
                        {
                            this.contact[i].id = this.contact[i].id.toString();
                        }
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            cancel(){
               this.$emit('freshData',{
                    type:'cancel',
                })
            },
            getNotificationInfo(){
              var params = {
                  id:this.editId
              };
                capis.getNotificationInfo(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        //对数据进行赋值
                        for(var key in this.form)
                        {
                            if(key == 'ContactId')
                            {
                                this.form[key] = data.res.data[key].split(',');
                            }
                            else
                            {
                                this.form[key] = data.res.data[key];
                            }
                            
                        }
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            getWarnType(){
                this.warningType = ctool.getWarningType();
            }
        },
        created(){
            this.getWarnType();
            this.getContactListEnable();
            this.getNotificationInfo();
        }
    }
</script>
<style scoped lang="scss">
   .warning-add{
       padding: 0 80px;
       .warning-add-title{
           font-size: 28px;
           font-weight: 600;
           height: 60px;
           line-height: 60px;
       }
   }
</style>


