<template>
   <div class="control">
      <div class="control_left">
          <div class="menu_show">
              <p class="menu_title">主机总数</p>
              <p class="menu_list">
                  <i class="el-icon-success success"></i>
                  <span>正常</span>
                  <span>21</span>
              </p>
              <p class="menu_list">
                  <i class="el-icon-warning warning"></i>
                  <span>警告</span>
                  <span>0</span>
              </p>
              <p class="menu_list">
                  <i class="el-icon-remove error"></i>
                  <span>危险</span>
                  <span>0</span>
              </p>
              <p class="menu_list">
                  <i class="el-icon-question question"></i>
                  <span>未知</span>
                  <span>0</span>
              </p>
          </div>
          <div class="menu_show">
              <p class="menu_title">服务总数</p>
              <p class="menu_list">
                  <i class="el-icon-success success"></i>
                  <span>正常</span>
                  <span>58</span>
              </p>
              <p class="menu_list">
                  <i class="el-icon-warning warning"></i>
                  <span>警告</span>
                  <span>0</span>
              </p>
              <p class="menu_list">
                  <i class="el-icon-remove error"></i>
                  <span>危险</span>
                  <span>0</span>
              </p>
              <p class="menu_list">
                  <i class="el-icon-question question"></i>
                  <span>未知</span>
                  <span>0</span>
              </p>
          </div>
      </div>
      <div class="control_right">
          <!-- 地球 -->
          <GL></GL>
      </div>
        
   </div>
</template>
<script>
    import GL from './GL.vue'
    export default{
        data(){
            return{
                dialogShow:false
            }
        },
        components:{
            GL
        }
    }
</script>
<style scoped lang="scss">
   .control{
       height: 680px;
       background: #fff;
       border-radius: 20px;
       box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
       display: flex;
       justify-content: space-around;
       .control_left{
            width: 310px;
        //    background-color: red;
            .menu_show{
                padding-left: 80px;
                padding-top: 37px;
                font-size: 18px;
                font-weight: 500;
                // font-family:sans-serif,
                .menu_title{
                    margin-bottom: 24px;
                    font-weight: 600;
                }
                .menu_list{
                    height: 38px;
                    line-height: 38px;
                    span{
                        margin-left: 20px;
                    }
                    .success{
                        color: #95d871;
                    }
                    .warning{
                        color:#fed361
                    }
                    .error{
                        color: #ff3838;
                    }
                    .question{
                        color: #999999;
                    }
                }
            }
       }
       .control_right{
        //    background-color: blue;
           flex: 1;
       }
   }
</style>


