 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-21 10:54:42
-->
<template>
       
    <div class="content-box x6">
        <p class="content-box-title">资源详情</p>
        <el-table
            ref="record-table"
            :data="list"
            style="width: 100%"
            stripe
            max-height="400"
            highlight-current-row
        >
            <el-table-column
            label="设备名称"
                prop="name"
            show-overflow-tooltip
            >
            </el-table-column>
            <el-table-column
            label="IP"
                prop="host"
            sortable
            show-overflow-tooltip
            width="120"
            >
            </el-table-column>
            <el-table-column
            label="CPU使用率"
            sortable
            show-overflow-tooltip
            width="80"
            >
            <template slot-scope="scope">
                        {{scope.row.cpuUse}}%
                </template>
            </el-table-column>
            <el-table-column
            label="内存使用率"
            show-overflow-tooltip
            width="80"
            >
                <template slot-scope="scope">
                        {{scope.row.memUsedPercent}}%
                </template>
            </el-table-column>
            
            <el-table-column
            label="负载"
            show-overflow-tooltip
            >
             <template slot-scope="scope">
                    <span>
                        <el-tag size="mini">{{scope.row.cpuLoad1}}</el-tag>
                        <el-tag size="mini" type="success">{{scope.row.cpuLoad5}}</el-tag>
                        <el-tag size="mini" type="info">{{scope.row.cpuLoad15}}</el-tag>
                    </span>
                </template>
            </el-table-column>
            <el-table-column
            label="网络"
            show-overflow-tooltip
            >
             <template slot-scope="scope">
                    <div>
                        <span><i class="el-icon-caret-bottom" style="color:#05f360;margin-right:5px;"></i>{{scope.row.netSpeed.in.size}} {{scope.row.netSpeed.in.format}}/s</span>
                    </div>
                    <div>
                        <span><i class="el-icon-caret-top" style="color:#05f360;margin-right:5px;"></i>{{scope.row.netSpeed.out.size}} {{scope.row.netSpeed.out.format}}/s</span>
                    </div>
                </template>
            </el-table-column>
            <el-table-column
            label="通信状态"
            sortable
            show-overflow-tooltip
            width="100"
            >
                <template slot-scope="scope">
                    <div class="dedevice-run">
                        <p style="width:12px;height:12px;border-radius:50%;margin-right:5px;" :style="{backgroundColor:(scope.row.running?'#05f360':'red')}"></p>
                        <p v-if="scope.row.running">在线</p>
                        <p v-if="!scope.row.running">离线</p>
                    </div>
                </template>
            </el-table-column>
        </el-table>
    </div>
      
</template>
<script>
    export default{
        data(){
            return{
                list:null
            }
        },
        created(){
            this.getResourseList();
            this.$nextTick(()=>{
                this.initWs();
            })
        },
        methods:{
            //获取到详情
            getResourseList(){
                var params = {}
                capis.getResourseList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            initWs(){
                //检测主机状态
                this.$LaravelEcho.instance.channel('HostStatus').listen('HostEvent', (e) => {
                    if(e.type=="status")
                    {
                        if(this.list.length>0)
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].running = e.data.running;
                                }
                            });
                        }
                    }
                    else if(e.type=="HostAdd" || e.type=="HostDel" || e.type=="HostChange")
                    {
                        this.getResourseList();
                    }
                })
                this.$LaravelEcho.instance.channel('SysInfo').listen('SysInfoEvent', (e) => {
                    if(this.list.length>0)
                    {
                        //检测cpu的使用率
                        if(e.type=="CpuInfo")
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].cpuUse = e.data.cpuUse;
                                    this.list[index].cpuLoad1 = e.data.cpuLoad1;
                                    this.list[index].cpuLoad5 = e.data.cpuLoad5;
                                    this.list[index].cpuLoad15 = e.data.cpuLoad15;
                                }
                            });
                        }
                        //内存的使用率
                        else if(e.type =='MemoryInfo')
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].memUsedPercent = e.data.memUsedPercent;
                                }
                            });
                        }
                        //网络
                        else if(e.type =='ifInfo')
                        {
                            this.list.forEach((value,index) => {
                                if(value.id==e.host_id)
                                {
                                    this.list[index].netSpeed.in = e.data.escape.in; //下行量
                                    this.list[index].netSpeed.out = e.data.escape.out; //上行量
                                }
                            });
                        }
                    }

                })
            }
        },
        components:{
        },
        beforeDestroy(){
            console.log("退出监听通道:SysInfo");
            this.$LaravelEcho.instance.leaveChannel('SysInfo');
            console.log("退出监听通道:HostStatus");
            this.$LaravelEcho.instance.leaveChannel('HostStatus');
        }
    }
</script>
<style scoped lang="scss">
   
        .content-box{
            box-sizing: border-box;
            background-color: #fff;
            box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            padding: 20px 40px;
            border-radius: 20px;
            margin-bottom: 20px;
            .content-box-title{
                font-size: 14px;
                color: #666;
                margin-bottom: 20px;
            }
            .item-box{
                display: flex;
                flex-wrap: wrap;
                justify-content: flex-start;
                .item{
                     box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
                     padding: 10px 20px;
                     border-radius: 10px;
                     margin-bottom: 20px;
                     margin-right: 20px;
                    .item-top{
                        display: flex;
                        justify-content: space-between;
                        .item-top-name{
                            font-size: 12px;
                            color: #999;
                        }
                        .item-top-icon{
                            font-size: 14px;
                        }
                    }
                    .item-top-data{
                        font-size: 18px;
                        margin-top: 10px;
                    }
                }
            }
        }
        .content-box.x6{
            width: 59%;
            // margin-right: 20px;
            .item-box{
                .item{
                    width: 18%;
                }
            }
        }
        .content-box.x4{
            width: 40%;
            .item-box{
                .item{
                    width: 22%;
                }
            }
        }
   
        .dedevice-run{
            display: flex;
            align-items: center;
        }
</style>


