 * @Author: GaoX
 * @Date: 2022-04-21 12:18:09
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-19 15:15:32
-->
<template>
    <div class="servicesinfo-list">
        <DeviceInfo :hostId='hostId'></DeviceInfo>
        <LineStatus :hostId='hostId'></LineStatus>
        <!-- <Manage :hostId='hostId'></Manage> -->
    </div>
</template>
<script>
    import LineStatus from './lineStatus.vue'
    import Manage from './mange.vue'
    import DeviceInfo from './deviceinfo.vue'
    export default{
        data(){
            return{
               hostId:''
            }
        },
        methods:{
           
        },
        components:{
            LineStatus
            ,Manage
            ,DeviceInfo
        },
        created(){
            this.hostId=this.$route.query.hostId;
        },
    }
</script>

<style scoped lang="scss">
    .servicesinfo-list{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 80px;
        .top-tilte{
            .serviceinfo-title{
                display: flex;
                font-size: 20px;
                font-weight: 600;
                .serviceinfo-back{
                    i{
                        font-size: 30px;
                        color: #ceceee;
                    }
                }
                .serviceinfo-border{
                    width: 10px;
                    height: 26px;
                    background-color: #5e5ec6;
                    border-radius: 5px;
                    margin-left: 20px;
                }
                .serviceinfo-tit{
                    margin-left: 20px;
                }
            }
            .service-name{
                .sevice-name-info{
                    font-size: 22px;
                    font-weight: 600;
                    margin-top: 20px;
                }
                .sercvice-device-type{
                    font-size: 14px;
                    color: #999999;
                    margin-top: 20px;
                }
            }

        }
        .serviceinfo-content{
           .serviceinfo-title{
                display: flex;
                font-size: 20px;
                font-weight: 600;
                margin-bottom: 20px;
                .serviceinfo-border{
                    width: 10px;
                    height: 26px;
                    background-color: #5e5ec6;
                    border-radius: 5px;
                    margin-left: 20px;
                }
                .serviceinfo-tit{
                    margin-left: 18px;
                }
            } 
        }
       
    }
</style>


