<!--
 * @Descripttion: 流量
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-24 14:33:02
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-24 18:12:09
-->
<template>
    <div class="serviceinfo-content">
        <div class="serviceinfo-title">
            <div class="left">
                <p class="serviceinfo-border"></p>
                <p class="serviceinfo-tit">流量</p>
            </div>
            <div class="right">
                 <el-date-picker
                    v-model="time"
                    type="datetimerange"
                    range-separator="~"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    align="right"
                    size="mini"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    @change="changeTime"
                    >
                </el-date-picker>
            </div>
        </div>
        <div class="service-line">
             <div class="line-info">
                <div class="line">
                    <!-- 折线图 -->
                    <div class="" id="liuliang" :style="{ height: '360px','margin-bottom':'20px'}"></div>
                </div>
            </div>
        </div>
        <el-divider></el-divider>
    </div>
</template>
<script>
    export default{
        props:{
            hostId:{
                default:'',
            }
        },
        data(){
            return{
                xTitle:null,
                xData1:[],
                xData2:[],
                echarts:null,
                time:'',
                option:null,
                startTime:'',
                endTime:'',
            }
        },
        methods:{
           initEchats(){
                this.option = {
                    xAxis: {
                        type: 'category',
                        data: this.xTitle,
                        axisPointer: {
                            lineStyle: {
                                color: '#d35454', //提示线颜色
                                width: 2
                            },
                        },
                    },
                    yAxis: {
                        type: 'value',
                        axisLabel: {
                            formatter: (value, index)=> {
                                console.log(value);
                                var size = this.getSize(value, true);
                                return size.int + " " + size.format;
                            }
                        }
                    },
                    tooltip: {
                        trigger: 'axis',
                        formatter:(params)=>{
                            var inSize = this.getSize(params[0].data, true);
                            var outSize = this.getSize(params[1].data, true);
                            return `${params[0].name}<br/>${params[0].marker}${params[0].seriesName}: ${inSize.float} ${inSize.format}/s<br/>${params[0].marker}${params[1].seriesName}: ${outSize.float} ${outSize.format} /s`
                        }
                    },
                    legend:{
                        data:['上行','下行']
                    },
                    toolbox: {
                        feature: {
                            saveAsImage: {},
                            restore:{}
                        }
                    },
                    dataZoom: [
                        {
                            type: 'inside',
                        },
                    ],
                    grid:{
                        x:50, //左侧未知
                        x2:0 //右侧
                    },

                    series: [
                        {
                            name:'上行',
                            data: this.xData1,
                            type: 'line',
                            smooth: true,
                            itemStyle:{
                                color:'#d35454' //线的颜色
                            },
                            areaStyle:{
                                normal:{
                                    color:new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                        {
                                            offset: 0,
                                            color: 'rgba(211,84,84,0.5)' //渐变上
                                        },
                                        {
                                            offset: 1,
                                            color: 'rgba(211,84,84,0)' //渐变下
                                        }
                                    ]
                                )}
                            }
                        },
                        {
                            name:'下行',
                            data: this.xData2,
                            type: 'line',
                            smooth: true,
                            itemStyle:{
                                color:'rgba(72,142,237,1)' //线的颜色
                            },
                            areaStyle:{
                                normal:{
                                    color:new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                        {
                                            offset: 0,
                                            color: 'rgba(72,142,237,0.5)' //渐变上
                                        },
                                        {
                                            offset: 1,
                                            color: 'rgba(72,142,237,0)' //渐变下
                                        }
                                    ]
                                )}
                            }
                        }
                    ]
                };
                this.echarts = this.$echarts.init(document.getElementById('liuliang'));
                this.echarts.setOption(this.option);
            },
             getHostInfoEchart(){
                var params = {
                    hostId:this.hostId,
                    type:5,
                    dateType:1,
                    startTime:this.startTime,
                    endTime:this.endTime
                }
                capis.getHostInfoEchart(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.xTitle = data.res.header.reverse();
                        this.xData1 = data.res.data[1].reverse();
                        this.xData2 = data.res.data[0].reverse();
                        this.echarts.setOption({
                                    series:[{
                                        data: this.xData1
                                    },
                                    {
                                        data: this.xData2
                                    }],
                                    xAxis:{data:this.xTitle}
                                });
                        
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            },
            changeTime(time){
                this.startTime = time[0];
                this.endTime = time[1];
                this.getHostInfoEchart();
            },
            getSize(size,byte=false){
                var data = {"int":size,"float":size,"format":byte?"byte":"KB"};
                var sizeInt = Math.ceil(size);
                if(byte) {
                    if(sizeInt>1099511627776)
                    {
                        var sizeTmp = (sizeInt / 1099511627776 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"TB"};
                    }
                    else if(sizeInt>1073741824)
                    {
                        var sizeTmp = (sizeInt / 1073741824 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"GB"};
                    }
                    else if(sizeInt>1048576)
                    {
                        var sizeTmp = (sizeInt / 1048576 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"MB"};
                    }
                    else if(sizeInt>1024)
                    {
                        var sizeTmp = (sizeInt / 1024 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"KB"};
                    }
                    else
                    {
                        data = {"int":size,"float":size,"format":"byte"};
                    }
                }
                else {
                    if(sizeInt>1073741824)
                    {
                        var sizeTmp = (sizeInt / 1073741824 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"TB"};
                    }
                    else if(sizeInt>1048576)
                    {
                        var sizeTmp = (sizeInt / 1048576 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"GB"};
                    }
                    else if(sizeInt>1024)
                    {
                        var sizeTmp = (sizeInt / 1024 * 100) /100;
                        data = {"int":Math.ceil(sizeTmp),"float":sizeTmp.toFixed(2),"format":"MB"};
                    }
                    else
                    {
                        data = {"int":size,"float":size,"format":"KB"};
                    }
                }
                return data;
            },
            initWs(){
                this.$LaravelEcho.instance.channel('SysInfo').listen('SysInfoEvent', (e) => {
                    //检测cpu的使用率
                    if(e.type=="ifInfo")
                    {
                        if(this.hostId==e.host_id)
                        {
                            this.xTitle.push(ctool.getYMDHMS(e.time));
                            this.xData1.push(e.data.original.out);
                            this.xData2.push(e.data.original.in);
                            this.echarts.setOption({
                                series: [
                                    {
                                        data: this.xData1
                                    },
                                    {
                                        data: this.xData2
                                    }
                                ],
                                xAxis:{data:this.xTitle}
                            });
                        }
                    }
                })
            }
        },
        components:{
        },
        created(){
            this.getHostInfoEchart();
            this.$nextTick(()=>{
                this.initEchats();
                this.initWs();
            })
        },
    }
</script>

<style scoped lang="scss">
    
    .serviceinfo-content{
        width: 50%;
        .serviceinfo-title{
            display: flex;
            justify-content: space-between;
            font-size: 20px;
            font-weight: 600;
            margin-bottom: 20px;
            .left{
                display: flex;
            }
            .serviceinfo-border{
                width: 10px;
                height: 26px;
                background-color: #5e5ec6;
                border-radius: 5px;
                margin-left: 20px;
            }
            .serviceinfo-tit{
                margin-left: 18px;
            }
        } 
    }
       
</style>


