 * @Descripttion: your project
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 23:06:31
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-21 11:06:42
-->
<template>
   <div class="top">
       <div class="left">
           <router-link to="/index/welcom" class="index-iocn" tag="p">
                <div class="icon" :class="[activeIconClass]">
                    
                </div>
           </router-link>
       </div>
       <div class="right">
           <div class="time">
               <!-- 时间 -->
               {{time.hours}}<span :style="{visibility:time.minutesflag?'visible':'hidden'}">:</span>{{time.minutes}}
           </div>
            <div class="date">
                <!-- 年月日 -->
                <p>{{time.yyyMMdd}}</p>
                <!-- 星期 -->
                <p>{{time.week}}</p>
            </div>
            <div class="notice" @click="routeToMessage" :class="[activeMessageClass]">
               <el-tooltip class="item" effect="light" content="报错与告警" placement="bottom">
                <el-badge is-dot class="item" :hidden="incomplete<=0">
                        <i class="el-icon-bell"></i>
                </el-badge>
               </el-tooltip>
           </div>
           <div class="user">
                 <el-dropdown trigger="click">
                    <span class="el-dropdown-link">
                        <i class="el-icon-user"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-if="userinfo" icon="el-icon-user-solid">{{userinfo.name}}</el-dropdown-item>
                        <el-dropdown-item v-if="userinfo" icon="el-icon-chat-dot-square">{{userinfo.email}}</el-dropdown-item>
                        <el-dropdown-item v-if="userinfo" icon="el-icon-time">{{userinfo.updated_at}}</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
           </div>
            <div class="loginout" style="margin-right:20px;" @click="loginOut">
                <i class="el-icon-switch-button"></i>
           </div>
       </div>
   </div>
</template>
<script>
    export default{
        data(){
            return{
                activeIconClass:'',
                activeMessageClass:'',
                getTimeintervalObj:null, //时间定时器的对象
                time:{
                    yyyMMdd:'', //日期 年月日
                    week:'', //周
                    hours:'', //小时
                    minutes:'', //分数
                    minutesflag:true
                },
                userinfo:null,
                incomplete:0,//未读消息数
            }
        },
        created(){
            // 执行计时器，显示时间
            this.getTimes();
            //获取到用户信息
            this.getAuthMe();
            //获取到未读消息数
            this.getNoticeIncomplete();
        },
        methods:{
            routeToMessage(){
                this.$router.push({path:'/index/message'})
            },
            getRoutePath(to,from){
                if(this.$route.path == '/index/welcom')
                {
                    this.activeIconClass='active';
                }
                else
                {
                    this.activeIconClass='';
                }
                 if(this.$route.path == '/index/message')
                {
                    this.activeMessageClass='active';
                }
                else
                {
                    this.activeMessageClass='';
                }
            },
            getTimes(){
                // console.log(82)
                this.getTimeintervalObj=setTimeout(()=>{
                    // 定时器 一秒执行一次
                    var myDate = new Date();
                    if((parseInt(myDate.getMonth())+1)<10)
                    {
                        var months = '0'+(parseInt(myDate.getMonth())+1);
                    }
                    else
                    {
                        var months =  parseInt(myDate.getMonth())+1;
                    }
                    this.time.yyyMMdd = myDate.getFullYear()+'-'+months+'-'+myDate.getDate(); //年月日
                    var weeksDate=['星期日','星期一','星期二','星期三','星期四','星期五','星期六']
                    this.time.week= weeksDate[myDate.getDay()]; //周
                    this.time.hours = myDate.getHours();//小时
                    this.time.minutes = myDate.getMinutes();//分钟
                    if(this.time.minutes<10)
                    {
                        this.time.minutes = '0'+this.time.minutes;
                    }
                    //判断是否显示两个点
                    this.time.minutesflag=!this.time.minutesflag;
                    this.getTimes();
                },1000);
                

            },
            loginOut(){
                this.$confirm('确定要退出？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'error'
                    }).then(() => {
                        var loading = this.$loading({
                            lock: true,
                            text: 'Loading',
                            spinner: 'el-icon-loading',
                            background: 'rgba(0, 0, 0, 0.7)'
                        })
                        var params = {}
                        capis.logout(params,{})
                        .then(data=>{
                            loading.close();
                            if(data.status == 'success'){
                            ctool.gotLogin({
                                title: '退出成功',
                                message: '感谢使用！',
                                type: 'success'
                            });
                            }else{
                            this.$notify({
                                title: '登录失败',
                                message: '账号或密码不正确',
                                type: 'warning'
                            });
                            }
                        })
                        .catch(err=>{
                            loading.close();
                            this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                            });
                        })
                    });
                
            },
            getAuthMe(){
                //获取到个人信息
                var params = {}
                capis.getAuthMe(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        this.userinfo = data.res;
                    }else{
                    
                    }
                })
                .catch(err=>{
                    loading.close();
                    this.$notify({
                    title: '提示',
                    message: '操作失败',
                    type: 'warning'
                    });
                })
            },
            getNoticeIncomplete(){
                var params = {}
                capis.getNoticeIncomplete(params,{})
                .then(data=>{
                    if(data.status == 'success'){
                        this.incomplete = data.res.data;
                    }else{
                    
                    }
                })
                .catch(err=>{
                    loading.close();
                    this.$notify({
                    title: '提示',
                    message: '操作失败',
                    type: 'warning'
                    });
                })
            }
        },
        watch:{
            '$route':'getRoutePath'
        }
    }
</script>
<style scoped lang="scss">
   .top{
        height: 100%;
        display: flex;
        justify-content: space-between;
        box-shadow: 6px 0px 5px   rgba(0,0,0,0.1);
       .left{
           height: 100%;
           color: #5e5ec6;
           display: flex;
           align-items: center;
           margin-left: 20px;
           .index-iocn{
                cursor: pointer;
                .icon 
                {
                    background:#fff url(../../../assets/index/home.png) no-repeat 3px 4px / 21px 19px;
                    width: 28px;
                    height: 28px;
                    border-radius: 5px;
                    box-shadow: 0 0 15px 0 rgba(0,0,0,0.1);
                }
                //激活状态
                .icon.active{
                    background:#5e5ec6 url(../../../assets/index/home-active.png) no-repeat 3px 4px / 21px 19px;
                }
           }
           .other-menu
           {
                height: 32px;
                cursor: pointer;
                margin-left: 20px;
                line-height: 30px;
           }
       }
       .right{
           display: flex;
           align-items: center;
           color: #5d73c7;
           margin-right: 20px;
           .notice{
               width: 32px;
               height: 32px;
               background-color: #fff;
               text-align: center;
               border-radius: 5px;
               box-shadow: 0 0 15px 0 rgba(0,0,0,0.1);
               margin-left: 20px;
               cursor: pointer;
               i{
                   font-size: 22px;
                   line-height: 32px;
                   color: #5d73c7;
               }
           }
           .notice.active{
               background-color: #5d73c7;
               i{
                   color: #fff;
               }
           }
           .time{
               margin-left: 35px;
               font-size: 22px;
               margin-right: 10px;
           }
           .date{
               border-left: 3px solid #5d73c7;
               border-radius: 2px;
               padding-left: 10px;
               font-size: 16px;
           }
           .user,.loginout{
               width: 32px;
               height: 32px;
               background-color: #fff;
               text-align: center;
               border-radius: 5px;
               box-shadow: 0 0 15px 0 rgba(0,0,0,0.1);
               margin-left: 20px;
               cursor: pointer;
               i{
                   font-size: 22px;
                   line-height: 32px;
                   color: #5d73c7;
               }
           }
       }
   }
</style>


