 * @Descripttion: 首页
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 23:06:31
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-21 10:55:01
-->
<template>
       <div class="content-box x4">
           <p class="content-box-title">告警信息</p>
           <el-card class="box-card" v-for="(item,index) in list" :key="index">
                <div class="text item">
                    <div class="box-content">
                        <div class="lable">主机名称：</div>
                        <div class="content">{{item.host}}</div>
                    </div>
                    <div class="box-content">
                        <div class="lable">主机IP：</div>
                        <div class="content">{{item.hostIp}}</div>
                    </div>
                    <div class="box-content">
                        <div class="lable">告警内容：</div>
                        <div class="content">{{item.notification}}</div>
                    </div>
                    <div class="box-content">
                        <div class="lable">告警时间：</div>
                        <div class="content">{{item.updated_at}}</div>
                    </div>
                    <div class="box-content">
                        <div class="lable">处理状态：</div>
                        <div class="content" :style="{color:item.status==2?'#9cdb7a':'#ff3838'}">{{item.status ==1 ?'待处理':'已处理'}}</div>
                    </div>
                </div>
            </el-card>
       </div>
</template>
<script>
    
    export default{
        data(){
            return{
                list:[]
            }
        },
        created(){
            this.getHomeWarningList();
        },
        methods:{
            getHomeWarningList(){
                var params = {}
                capis.getHomeWarningList(params,{})
                .then(data=>{
                    this.tableLoading = false;
                    if(data.status == 'success'){
                        //对数据进行赋值
                        this.list = data.res.data;
                    }else{
                        this.$notify({
                            title: '提示',
                            message: data.des,
                            type: 'warning'
                        });
                    }
                
                })
                .catch(err=>{
                    this.tableLoading = false;
                    this.$notify({
                        title: '提示',
                        message: '操作失败',
                        type: 'warning'
                    });
                })
            }
        },
        components:{
        }
    }
</script>
<style scoped lang="scss">
   
        .content-box{
            box-sizing: border-box;
            background-color: #fff;
            box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
            padding: 20px 40px;
            border-radius: 20px;
            margin-bottom: 20px;
            max-height: 400px;
            .content-box-title{
                font-size: 14px;
                color: #666;
                margin-bottom: 20px;
            }
            .item-box{
                display: flex;
                flex-wrap: wrap;
                justify-content: flex-start;
                .item{
                     box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
                     padding: 10px 20px;
                     border-radius: 10px;
                     margin-bottom: 20px;
                     margin-right: 20px;
                    .item-top{
                        display: flex;
                        justify-content: space-between;
                        .item-top-name{
                            font-size: 12px;
                            color: #999;
                        }
                        .item-top-icon{
                            font-size: 14px;
                        }
                    }
                    .item-top-data{
                        font-size: 18px;
                        margin-top: 10px;
                    }
                }
            }
        }
       
        .content-box.x4{
            width: 40%;
            .item-box{
                .item{
                    width: 22%;
                }
            }
            
        }
    .box-card{
        margin-bottom: 20px;
    }
    .item{
        padding-bottom: 20px;
        .box-content{
            display: flex;
            justify-content: flex-start;
            font-size: 12px;
            height: 22px;
            line-height: 22px;
            overflow: hidden;
            
            .lable{
                text-align: right;
                font-weight: bolder;
                width: 80px;
            }
        }
    }
   
</style>


