<template>
   <div class="warninfo">
        <div class="top-tilte">
             <div class="serviceinfo-title">
                <p class="serviceinfo-back"><i class="el-icon-back"></i></p>
                <p class="serviceinfo-border"></p>
                <p class="serviceinfo-tit">Nigx服务</p>
            </div>
            <div class="top-right-title">
                CTI服务器
            </div>
        </div>
        <div>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" router>
                <el-menu-item index="/index/warninfo/errorlog">错误日志</el-menu-item>
                <el-menu-item index="/index/warninfo/faillog">失败日志</el-menu-item>
                <el-menu-item index="/index/warninfo/connectnumber">连接数</el-menu-item>
                <el-menu-item index="/index/warninfo/cpuuse">CPU使用</el-menu-item>
                <el-menu-item index="/index/warninfo/connectfail">连接失败数</el-menu-item>
            </el-menu>
            <router-view></router-view>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
               activeIndex:'/index/warninfo/errorlog'
            }
        },
        methods:{
           
        },
        mounted(){
        },
        components:{
        },
        computed:{
            
        }
    }
</script>

<style scoped lang="scss">
   .warninfo{
        box-sizing: border-box;
        background-color: #fff;
        box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.1);
        border-radius: 20px;
        padding: 40px 40px;
        .top-tilte{
            display: flex;
            justify-content: space-between;
            .serviceinfo-title{
                display: flex;
                font-size: 20px;
                font-weight: 600;
                .serviceinfo-back{
                    i{
                        font-size: 30px;
                        color: #ceceee;
                    }
                }
                .serviceinfo-border{
                    width: 10px;
                    height: 26px;
                    background-color: #5e5ec6;
                    border-radius: 5px;
                    margin-left: 20px;
                }
                .serviceinfo-tit{
                    margin-left: 20px;
                }
            }
            .top-right-title{
                font-size: 20px;
                font-weight: 600;
            }
        }
    }
</style>


