<template>
    <div class="content-box">
        <CpuLine :hostId="hostId"></CpuLine>
        <FuzaiLine :hostId="hostId"></FuzaiLine>
        <NeicunLine :hostId="hostId"></NeicunLine>
        <ConnectLine :hostId="hostId"></ConnectLine>
        <LiuLiangLine :hostId="hostId"></LiuLiangLine>
    </div>
    
</template>
<script>
    import CpuLine from './lines/cpu.vue'
    import FuzaiLine from './lines/fuzai.vue'
    import NeicunLine from './lines/neicun.vue'
    import ConnectLine from './lines/connect.vue'
    import LiuLiangLine from './lines/liuliang.vue'
    export default{
        props:{
            hostId:{
                default:''
            }
        },
        data(){
            return{
               pickerOptions: {
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                        picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '昨天',
                        onClick(picker) {
                        const date = new Date();
                        date.setTime(date.getTime() - 3600 * 1000 * 24);
                        picker.$emit('pick', date);
                        }
                    }, {
                        text: '一周前',
                        onClick(picker) {
                        const date = new Date();
                        date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', date);
                        }
                    }]
                },
            }
        },
        methods:{
           
        },
        components:{
            CpuLine
            ,FuzaiLine
            ,NeicunLine
            ,ConnectLine
            ,LiuLiangLine
        },
        mounted(){
        },
    }
</script>

<style scoped lang="scss">
    .content-box{
        display: flex;
        flex-wrap: wrap;
    }
    .serviceinfo-content{
        width: 50%;
        .serviceinfo-title{
            display: flex;
            justify-content: space-between;
            font-size: 20px;
            font-weight: 600;
            margin-bottom: 20px;
            .left{
                display: flex;
            }
            .serviceinfo-border{
                width: 10px;
                height: 26px;
                background-color: #5e5ec6;
                border-radius: 5px;
                margin-left: 20px;
            }
            .serviceinfo-tit{
                margin-left: 18px;
            }
        } 
    }
       
</style>


