 * @Descripttion: 人员添加
 * @version: 1.0
 * @Author: GaoX
 * @Date: 2022-04-11 23:06:31
 * @LastEditors: Please set LastEditors
 * @LastEditTime: 2022-05-12 11:49:10
-->
<template>
   <div class="warning-add">
       <p class="warning-add-title">基础选项</p>
       <el-form ref="form" :model="form" :rules="rules" label-width="100px">
            <el-form-item label="人员名称：" prop="name">
                <el-input v-model="form.name" clearable></el-input>
            </el-form-item>
           <el-form-item label="电话号码：" prop="phone">
                <el-input v-model="form.phone" clearable type="number"></el-input>
            </el-form-item>
            <el-form-item label="电子邮箱：" prop="email">
                <el-input v-model="form.email" clearable type="email"></el-input>
            </el-form-item>
            <el-form-item label="状　　态：" prop="status">
                <el-switch
                    v-model="form.status"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    :active-value='1'
                    :inactive-value='2'
                    >
                </el-switch>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('form')">确定</el-button>
                <el-button @click="cancel">取消</el-button>
            </el-form-item>
        </el-form>
   </div>
</template>
<script>
    export default{
        data(){
           
            return{
                form:{
                    name:'',
                    phone:'',
                    email:'',
                    status:'1',
                },
                 rules: {
                    name: [
                        { required: true, message: '请输入姓名', trigger: 'blur' },
                    ],
                    phone: [
                        { required: true, message: '请输入电话', trigger: 'blur' },
                    ],
                    email: [
                        { required: true, message: '请输入邮箱', trigger: 'blur' },
                    ],
                }
            }
        },
        components:{
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                if (valid) {
                    var params = {...this.form}
                    capis.contactSave(params,{})
                    .then(data=>{
                        if(data.status == 'success'){
                             this.$emit('freshData',{
                                type:'add',
                            })
                            this.$notify({
                                title: '提示',
                                message: '操作成功',
                                type: 'success'
                            });
                        }else{
                            this.$notify({
                                title: '提示',
                                message: data.des,
                                type: 'warning'
                            });
                        }
                    
                    })
                    .catch(err=>{
                        this.$notify({
                            title: '提示',
                            message: '操作失败',
                            type: 'warning'
                        });
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
                });
            },
            cancel(){
               this.$emit('freshData',{
                    type:'cancel',
                })
            },
        },
    }
</script>
<style scoped lang="scss">
   .warning-add{
       padding: 0 80px;
       .warning-add-title{
           font-size: 28px;
           font-weight: 600;
           height: 60px;
           line-height: 60px;
       }
   }
</style>


